        mealDate: [
          { required: true, message: '请选择设备使用日期' },
        ],
        mealTime: [
          { required: true, message: '请选择设备使用时间' },
        ]
      },
      user: '',
      dialogVisible: false,
      pageNum: 1,
      pageSize: 10,
      total: 0,
      form: [],
      isDateValid: "",
      isOrderPlaceValid: "",
      matchingItem: [],
      matchingDateItem: [],
      timePeriods: [],
      activeName: 'second',
    };
  },
  created() {
   
    this.user =localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}
    this.load()
      
  },
  methods: {
    indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
    handleClick() {
        // console.log(this.activeName);
        this.pageNum=1
