    //判断就餐日期是否在当前日期之前
    isDateBeforeToday(row) {
      //身份是管理员则可以取消预约
      // console.log(user.role.includes('管理员'))

    
     
      //1.如果管理员确认了则不能取消预约
      if(row.status ==='审核通过' ){
        return true;
      }
      //2.如果不是自己预约的，则不可以进行取消，也就是显示按钮不可用
      const user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}
      // console.log( user.role.includes('管理员') || row.userName === user.loginName)
      // console.log( row.userName === user.role)
      //去掉管理员，管理员只能在后台进行取消
      // if (user.role.includes('管理员') || row.userName === user.loginName) {
      if ( row.userName === user.loginName) {
          return false
      } 
      return true
      // else  if( user.role !== row.orderUser){
      //   return true;
      // } 
     
     
      
      // console.log(sDateTime)
      // return sDateTime <= now;
    },
    load() {
      
      this.request.get("/equipmentOrder/pageRecord"
      ,{
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          userName: this.user.loginName,
          type: this.activeName,
}
      })
        .then(
          res => {
            // console.log(res)
            this.tableData = res.data.records
            this.total = res.data.total

          }
        )
    },
    submit() {
      this.$refs.form.validate((valid) => {
        if (valid) {
          this.request.post("/equipmentOrder", this.form).then((res) => {
            //res为后台返回的结果
            if (res.code === "200") {
              this.$message.success("修改成功");
              this.dialogVisible = false;
              this.load();
            } else {
              this.$message.error("操作失败");
            }
          })
        } else {
          return false;
        }
      })
    },
    cancel() {
      this.dialogVisible = false;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleCurrentChange(pageNum) {
      this.pageNum = pageNum;
      this.load();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.load();
    },
    handleClose(key, keyPath) {
    },
    handleEdit(row) {
      this.modalType = 1;
      // 注意需要对当前行数据进行深拷贝，否则会出错
      this.form = JSON.parse(JSON.stringify(row));
      this.dialogVisible = true;
    },
    handleDelete(id) {
      console.log("id",id)
      this.$confirm("是否确定取消预约", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.request.delete("/equipmentOrder/" + id).then((res) => {
            if (res.code === "200") {
              this.$message({
                type: "success",
                message: "取消预约成功!",
              });
            } else {
              this.$message({
                type: "warning",
                message: "距离预约时间小于三十分钟，不可取消预约操作!",
              });
            }

            // 重新获取列表的接口
            this.load();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消操作",
          });
        });
    },
    todeviceorder() {
      this.$router.push({ path: '/deviceorderhome' })
    },
    handleDatePickerChange() {
      this.form.timePeriod = ''; // 清空预约时间段

      const selectedDate = this.form.startTime;
      const options = { year: 'numeric', month: '2-digit', day: '2-digit' };

      const formattedDate = selectedDate.toLocaleDateString('zh-CN', options).replace(/\//g, '-');

      if (this.isOrderPlaceValid) {
        this.matchingDateItem = this.matchingItem.filter(item => item.startTime === formattedDate);
        if (this.matchingDateItem.length > 0) {
          this.isDateValid = true;
          this.timePeriods = this.matchingDateItem.map(item => item.timePeriod);
          this.timePeriods = Array.from(this.timePeriods);
          this.timePeriods.forEach((timePeriod) => {
            // 在这里执行对每个 timePeriod 的操作
            this.options.forEach(option => {
              option.disabled = timePeriod.toString() === option.value.toString()
            });
            // 可以根据实际需求进行其他操作
          });
        } 

        this.isOrderPlaceValid = "";
        this.isDateValid = "";
      }
    },
  },
};
