<template>
  <el-dialog :visible.sync="viewDialogVisible" width="50%" :show-close="false">
    <div slot="title" class="dialog-title">
      <h1>用户详细信息</h1>
    </div>
    <!-- 显示用户的详细信息 -->
    <div class="center-content">
      <el-form :model="userDetails" label-width="0.6rem" font-size="0.4rem">
        <el-row >
          <el-col :span="12" class="vertical-line">
            <el-form-item label="部门：" style="font-size: 0.5rem;"><span style="font-size: 0.05rem;">{{ userDetails.department }}</span></el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="加入时间：">{{ userDetails.joinDate }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="姓名：">{{ userDetails.name }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="工号：">{{ userDetails.employeeId }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="性别：">{{ userDetails.gender }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="生日：">{{ userDetails.birthdate }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="年龄：">{{ userDetails.age }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="身份证："><span style="font-size: 0.05rem;">{{ userDetails.idCard }}</span></el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="专业构成：">{{ userDetails.majorComposition }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="文化程度：">{{ userDetails.educationLevel }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="专业：">{{ userDetails.major }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="毕业院校：">{{ userDetails.graduateSchool }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="职称名称：">{{ userDetails.titleName }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="职称级别：">{{ userDetails.titleLevel }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12" class="vertical-line">
            <el-form-item label="毕业时间：">{{ userDetails.graduateDate }}</el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="是否参保：">{{ userDetails.insured ? '是' : '否' }}</el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="是否专家：">{{ userDetails.professional ? '是' : '否' }}</el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <!-- 关闭按钮 -->
      <span slot="footer" class="dialog-footer" >
        <el-button @click="closeDetailsDialog" class="close">关闭</el-button>
      </span>
    </div>
  </el-dialog>
</template>

<script>
export default {
  props: {
    user: Object, // 从父组件传递的用户信息对象
  },
  data() {
    return {
      viewDialogVisible: false, // 初始时不可见
      userDetails: {
        department: '',
        joinDate: '',
        name: '',
        employeeId: '',
        gender: '',
        birthdate: '',
        age: '',
        idCard: '',
        majorComposition: '',
        educationLevel: '',
        major: '',
        graduateSchool: '',
        titleName: '',
        titleLevel: '',
        graduateDate: '',
        insured: false,
      },
    };
  },
  watch: {
    user: {
      handler(newUser) {
        this.userDetails = {
          department: newUser.department,
          joinDate: newUser.enterTime,
          name: newUser.name,
          employeeId: newUser.jobNum,
          gender: newUser.gender,
          birthdate: newUser.birth,
          age: newUser.age,
          idCard: newUser.idNum,
          majorComposition: newUser.profTitle,
          educationLevel: newUser.eduBackground,
          major: newUser.major,
          graduateSchool: newUser.gradInstitutions,
          titleName: newUser.professionalComposition,
          titleLevel: newUser.profQuality,
          graduateDate: newUser.gradTime,
          insured: newUser.isInsured,
        };
        this.viewDialogVisible = true; // 弹出对话框
      },
      immediate: true,
    },
  },
  methods: {
    closeDetailsDialog() {
      this.viewDialogVisible = false; // 关闭对话框
    },
  },
};
</script>

<style scoped>
/* 添加全局样式，让文字居中 */
.center-text {
  text-align: center;
  font-size: 0.13rem;
}

.dialog-title {
  text-align: center;
  font-size: 0.13rem;
  padding: 20px 0; 

}

.center-content {
  text-align: center;
  width: 70%; /* 调整宽度 */
  margin-left: 16%; /* 居中 */
  margin-top: -5%;
  font-size: 0.2rem;
}

.el-row {
  border: 1px solid #e0e0e0;
  padding-right: 0px 0;
  display: flex; /* 使用 Flex 布局 */
  align-items: center; /* 垂直居中 */
  height: 0.26rem;
font-size: 0.5rem;
}

.vertical-line {
  border-right: 1px solid #e0e0e0; /* 添加纵向线 */
font-size: 0.2rem;
}

.el-form-item {
  margin-bottom: 0px;
  background: linear-gradient(to right, #f5f5f5 40%, transparent 20%)
}

/* 使用 center-text 样式使表格中文字居中 */

body.el-form-item__label {
  background-color: #ccc;  /* 你想要的背景颜色 */
  padding: 0px;  /* 可以根据需要调整间距 */
  display: inline-block;  /* 让背景仅占据label的宽度 */
}
.close{
background-color:rgb(55, 115, 176);
color: #faf9f9; /* 文字颜色为白色 */
font-weight: bold; /* 字体加粗 */
margin-top: 0.3rem;
font-size: 0.07rem;
}
.close:hover{
background-color:#409EFF;
color: #faf9f9; /* 文字颜色为白色 */
font-weight: bold; /* 字体加粗 */
margin-top:0.3rem;
font-size: 0.07rem;
}
.alternate-row {
  background-color: #f5f5f5;
  font-size: 0.5rem;
}
</style>

