<template>
    <div class="manage">
        <!-- 页头区域 -->
        <div class="manage-header">
            <!--form搜索区-->
            <el-form :inline="true" :model="fileForm">
                <el-form-item>
                    <!-- <el-upload 
                        action=""
                        :http-request="uploadFile"  -->
                    <!-- <el-upload action="http://192.168.1.14:9090/file/upload"  -->
                    <el-upload action="http://192.168.1.14:9090/file/upload" :show-file-list="false"
                        :on-success="handleFileUploadSuccess" :on-error="handleFileUploadFail" style="display: inline-block"
                        :data="uploadData">
                        <el-button type="primary" class="ml-5">上传文件</el-button>
                    </el-upload>
                    <!-- <el-upload 
                        :action="uploadUrl"
                        :headers="uploadHeaders"
                        :show-file-list="false"
                        :on-success="handleFileUploadSuccess" :on-error="handleFileUploadFail"
                        style="display: inline-block">
                        <el-button type="primary" class="ml-5">上传文件</el-button>
                    </el-upload> -->
                </el-form-item>
                <el-form-item>
                    <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">批量删除</el-button>
                </el-form-item>
                <el-form-item style="margin-left: 400px;">
                    <el-input placeholder="请输入文件编号" v-model="fileForm.num" suffix-icon="el-icon-search"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="load" type="primary">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-input placeholder="请输入文件名" v-model="fileForm.name" suffix-icon="el-icon-search"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="load2" type="primary">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="warning" @click="reset">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
        <!-- 主要表格 -->
        <el-table :header-row-style="{ height: '50px' }" :data="tableData" style="width: 1240px;height:615px" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="70px" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="70px" align="center" :index='indexMethod'></el-table-column>
            <el-table-column prop="num" label="文件编号" width="300px" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="name" label="文件名称" width="400px" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="type" label="文件类型" width="70" align="center"></el-table-column>
            <el-table-column prop="founder" label="创建人" width="80" align="center"></el-table-column>
            <el-table-column label="操作" width="250" align="center">
                <template slot-scope="scope">
                    <!-- <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.num)">删除 -->

                    <el-button type="primary" size="mini" @click="watch(scope.row.path)">预览</el-button>
                    <el-button type="primary" size="mini" @click="download(scope.row.downloadpath)">下载</el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>

        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0" class="page">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[5, 10]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // uploadUrl: 'http://localhost:9090/file/upload',
            // uploadHeaders: {
            //     Authorization: this.getTokenFromLocalStorage()
            // },
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            multipleSelection: [],
            // founder: localStorage.getItem("founder") ? JSON.parse(localStorage.getItem("founder")) : {},
            pageSize: 10,
            fileForm: {
                num: "",
                name: '',
            },
            uploadData: {
                // 其他上传所需数据
                username: ''
            }
        }
    },
    created() {
        this.load()
    },
    mounted() {
        const user = localStorage.getItem('user');
        if (user) {
            const parsedUser = JSON.parse(user);
            // 假设登录名存储在loginName字段中，根据您的实际情况调整
            this.uploadData.username = parsedUser.loginName;
            console.log("username", this.uploadData.username)
        }
    },
    methods: {
        // getTokenFromLocalStorage() {
        //     const user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {};
        //     return user.token || '';
        // },
        // getUsername() {
        //     // 从localStorage中获取用户名
        //     return localStorage.getItem('username');
        // },
        reset() {
            this.fileForm.num = ""
            this.fileForm.name = ""
            this.load()
        },
        uploadFile(params) {
            console.log(111)
            console.log(params)
            let formData = new FormData();
            formData.append("file", params.file); //添加文件对象

            this.request.post("/file/upload", formData
                // ,{     
                //     headers: {
                //         "Content-Type": "multipart/form-data",//配置header，我们上传的文件编码格式就是multipart/form-data
                // },
                // }
            ).then(res => {
                if (res.code === 200) {
                    console.log("0.0")
                    // this.$message("成功了");
                }
            })
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/file/del/" + id).then(res => {
                    console.log(res)
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }
            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // this.axios.post("http://192.168.1.14:9090/file/del/batch", ids).then(res => {
                this.request.post("/file/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/file/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    num: this.fileForm.num,
                    name: this.fileForm.name

                }
            }).then(
                res => {
                    this.tableData = res.data.records
                    this.total = res.data.total
                    console.log("tableData", this.tableData)
                    console.log("total", this.total)
                }
            )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleFileUploadFail() {
            this.$message.error("文件名已存在，请勿重复上传")
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        watch(path) {
            // window.open(path + '#toolbar=0', '_blank' )
            // let url = "http://127.0.0.1:8012/onlinePreview?url=" + encodeURIComponent(Base64.encodeURI(path));
            let url = "http://192.168.1.14:8012/onlinePreview?url=" + path;
            window.open(url+ '#toolbar=0', '_blank');
        },
        download(path) {
            window.location.href = path
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}

.page {
    display: grid;
    grid-template-rows: auto 1fr auto;
}
</style>