<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";

export default {
    components: { HomeHeader, HomeFooter },
    data() {
        return {
            pageNum: 1,
            pageSize: 10,
            projectDatas:[],
            projectList: [],
            projectList1:[],
            fileList: [],
            fileList1:[],
            total: 0,
            dfid: 0,
            user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
            id: localStorage.getItem("projectId"),
            showBoxCard6: false,
            showBoxCard4:true,
            showMyProjectsList: false,
            showAllProjectsList: true,
            selectedProjectIndex:'',
        }
    },
   
    created() {
        this.request.get("/project").then(res => {
            this.projectList = [...res.data].reverse();
            this.projectList.sort((a, b) => a.level - b.level);
            this.total = res.data.total;
        });
        this.load();
        this.load1();
        this.toggleSelection(0);
    },
    methods: {
        showMyProjects() {
            localStorage.setItem("projectId", this.projectList1[0].projectId);
            this.id = localStorage.getItem("projectId");
            this.load();
            this.showMyProjectsList = true;
            this.showAllProjectsList = false;
            this.showBoxCard4 = false;
            this.showBoxCard6 = true;
            this.toggleSelection(0)
            this.pageNum = 1
        },
        showAllProjects() {
            localStorage.setItem("projectId", this.projectList[0].id),
            this.id = localStorage.getItem("projectId"),
            this.load();
            this.showMyProjectsList = false;
            this.showAllProjectsList = true;
            this.showBoxCard4 = true;
            this.showBoxCard6 = false;
            this.toggleSelection(0);
            this.pageNum = 1
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handlenewsUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        load() {
            this.request.get("/projectFile/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    projectId: this.id,
                }
            }).then(res => {
                this.fileList = res.data.records
                this.fileList1 = res.data.records
                this.total = res.data.total
            })
        },
        load1(){
            this.request.get("/projectUser/pagers",{
                params:{
                    pageNum:this.pageNum,
                    pageSize:this.pageSize,
                    userId:this.user.id
                }
            }).then(res => {
                this.projectList1 = res.data.records.slice().reverse()
            })
        },
        projectDetail(fileid, name) {
            this.pageNum = 1
            localStorage.setItem("projectId", fileid),
            localStorage.setItem("projectName", JSON.stringify(name));
            this.id = fileid;
            this.load()
            // this.$router.push({ path: '/projectdetail' })
        },
        handleRowClick(row) {
            for (const item of this.fileList) {
                if (item.fileName === row.fileName) {
                    // let url = "http://127.0.0.1:8012/onlinePreview?url=" + encodeURIComponent(Base64.encodeURI(item.path));
                    let url = "http://192.168.1.14:8012/onlinePreview?url=" + item.path;
                    window.open(url + '#toolbar=0', '_blank')
                    // window.location.href=item.path
                    break; // 找到匹配行后结束循环
                }
            }
        },
        handleButtonClick(row){
            for (const item of this.fileList1) {
                if (item.fileName === row.fileName) {
                    window.location.href=item.downloadPath
                    break; // 找到匹配行后结束循环
                }
            }
        },
        toggleSelection(index) {
            // 否则设置为当前点击的项目索引
            this.selectedProjectIndex = index;
        },  
    },
}
</script>
  
