import axios from 'axios'
import router from '@/router';

const request = axios.create({
    baseURL: 'http://192.168.1.14:9090',
    // baseURL: 'http://172.25.183.18:9090',
    // baseURL: 'http://localhost:9090',
    // baseURL: 'http://192.168.0.166:9090',
        // baseURL: 'http://192.168.0.153:9090',
        
    timeout: 5000
})


request.interceptors.request.use( config=>{
    config.headers['Content-Type'] = 'application/json;charset=utf-8';
    // if (config.url.includes("upload")) {
    //     // 如果请求的 URL 包含 "upload"，则直接返回原始的配置
    //     config.headers['Content-Type'] = 'multipart/form-data';
    //     // return config;
    //   }
    const user = sessionStorage.getItem("data");
    if(user){
        config.headers['token'] = JSON.parse(user).token;  // 设置请求头
    }
    return config
},error =>{
    return Promise.reject(error)
}
)

// response 拦截器
// 可以在接口响应后统一处理结果
request.interceptors.response.use(
    response => {
        let res = response.data;
        //返回的是路径
        // console.log("  //返回的是路径")
        // console.log(response)
        // if(res.includes("file/"))
        // {
        //     return res;
        // }
        // 如果是返回的文件
        if (response.config.responseType === 'blob') {
            return res
        }
        // 兼容服务端返回的字符串数据
        if (typeof res === 'string') {
            res = res ? JSON.parse(res) : res
        }
        return res;
    },
    error => {
        处理响应错误
        if (error.code === 'ECONNABORTED' && error.message.includes('timeout')) {
            sessionStorage.clear()
            // 请求超时，执行页面跳转到登录界面
            router.push({ path: '/login' });
        } else if (error.response && error.response.status === 401) {
            sessionStorage.clear()
            // 未授权的请求，执行页面跳转到登录界面
            router.push({ path: '/login' });
        } else {
            // 其他错误，显示通用错误提示
            // alert('发生错误：' + error.message);
            sessionStorage.clear()
            router.push({ path: '/login' });
        }
        return Promise.reject(error);
        }
)



export default request