                <el-select v-model="form.type" placeholder="请选择预约中心" @change="getAllSquare1"
                  style="width: 2rem; display: flex; align-items:center; ">
                  <el-option label="EMC中心" value="second" />
                  <el-option label="安规中心" value="third" />
