<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
export default {
    components: { HomeHeader, HomeFooter },
    data() {
        return {
            dialogVisible: false,
            tableData: [],
            multipleSelection: [],
            activeName:'first',
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            form: {
                examineName: '',
                model:'',
                number:'',
                examineTime:'',
                type:'',
                remarks:''
            },
            verPlanForm: {
                examineName: '',
             
            },
            rules: {
                examineName: [
                    { required: true, message: "请输入设备名称", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "请选择型号", trigger: "blur" }
                ],
             
                examineTime: [
                    { required: true, message: "请选择核查时间", trigger: "blur" }
                ],
               
            },

            //TODO：可能需要按顺序排序，加入更多型号
            modelOptions: ['ESCI','FSV 30','VULB9163','BBHA 9120D','FPA-6592G','8449B',
                            'SG6000','150W1000','BLMA1060-200','STLP 9128D','FL7006','PM2002'],
            numberOptions: ['100983','101374','1178','920','060020','3008A02604',
                            '0327336','0332731','1711332','17','0332270','0328313'],
            placeOptions: [{
                value: 'first',
                label: '计量工程中心送检计划'
                }, {
                value: 'second',
                label: 'EMC中心仪器送检计划'
                }, {
                value: 'third',
                label: '安规中心仪器送检计划'
                },  {
                value: 'fourth',
                label: '安规中心功能性检查送检计划'
                }]

            // rules:{
                
            // }

        }
    },
    created() {
        this.load()
    },
    methods: {
        handleClick() {
            // console.log(this.activeName);
            this.pageNum=1
            this.load()

        },
        indexMethod(index) {
        let curpage = this.pageNum   //单前页码，具体看组件取值
        let limitpage = this.pageSize   //每页条数，具体是组件取值
        return (index + 1) + (curpage - 1) * limitpage
        },
        reset() {
            this.verPlanForm.examineName = ""
            this.load()
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/examinePlan/page", {
                    params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.verPlanForm.examineName,
                    type:this.activeName
                    }
                }).then(res => {  
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        reset() {
            this.verPlanForm.examineName = ""
            this.load()
        },
        // submit() {
        //   this.request.post("/order", this.form).then((res) => {
        //     //res为后台返回的结果
        //     if (res.code === "200") {
        //       this.$message.success("修改成功");
        //       this.dialogVisible = false;
        //       this.load();
        //     } else {
        //       this.$message.error("操作失败");
        //     }
        //   });
        // },
        cancel() {
            this.dialogVisible = false;
        },
        handleSelectionChange(val) {
            console.log(val);
            this.multipleSelection = val;
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum;
            this.load();
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize;
            this.load();
        },
        handleClose(key, keyPath) {
            console.log(key, keyPath);
        },
        handleEdit(row) {
            this.modalType = 1;
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row));
            this.dialogVisible = true;
        },
        handleDelete(id) {
            this.$confirm("是否确定取消预约", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            })
                .then(() => {
                    this.request.delete("/order/" + id).then((res) => {
                        if (res.code === "200") {
                            this.$message({
                                type: "success",
                                message: "操作成功!",
                            });
                        } else {
                            this.$message({
                                type: "error",
                                message: "距离就餐时间小于三十分钟，不可取消预约操作!",
                            });
                        }

                        // 重新获取列表的接口
                        this.load();
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "已取消操作",
                    });
                });
        },
        devicelist() {
            this.$router.push({ path: '/devicehome/devicelist' })
        },
        deviceinspplan() {
            this.$router.push({ path: '/devicehome/deviceinspplan' })
        },
        FixedAssets(){
        this.$router.push({ path: '/devicehome/FixedAssets' })
      },
    },
};
</script>
  
