<template>
    <div class="manage">
        <!-- 设备新增表单 -->
        <el-dialog title="设备新增表单" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- 设备的表单信息 -->
            <el-form ref="formDialog" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="固定资产编号" prop="id">
                    <el-input placeholder="请输入固定资产编号" v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item label="设备状态" prop="equipStatus">
                    <el-select v-model="form.equipStatus" placeholder="请选择设备状态">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="设备种类" prop="equipType">
                    <el-select v-model="form.equipType" placeholder="请输入设备种类">
                        <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="设备名称" prop="equipName">
                    <el-input placeholder="请输入设备名称" v-model="form.equipName"></el-input>
                </el-form-item>
                <el-form-item label="所在部门" prop="department">
                    <el-input placeholder="请输入所在部门" v-model="form.department"></el-input>
                </el-form-item>
                <el-form-item label="价格">
                    <el-input placeholder="请输入价格" v-model="form.price"></el-input>
                </el-form-item>
                <el-form-item label="型号">
                    <el-input placeholder="请输入型号" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="制造商" prop="manuFact">
                    <el-input placeholder="请输入制造商" v-model="form.manuFact"></el-input>
                </el-form-item>
                <el-form-item label="验收日期">
                    <el-date-picker v-model="form.acceptDate" type="date" placeholder="请选择验收日期"  value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="生产日期" >
                    <el-date-picker v-model="form.prodDate" type="date" placeholder="请选择生产日期" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="出厂编号">
                    <el-input placeholder="请输入出厂编号" v-model="form.facNum"></el-input>
                </el-form-item>
                <el-form-item label="设备文档">
                    <el-input placeholder="请上传设备文档" v-model="form.purchCont"> </el-input>
                    <el-upload action="http://192.168.1.14:9090/fileEquipment/upload" :show-file-list="false"
                        :on-success="handleFileUploadSuccess" style="display: inline-block">
                        <el-button type="primary" size="mini" class="ml-5">上传文件</el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="国别">
                    <el-input placeholder="请输入国别" v-model="form.country"></el-input>
                </el-form-item>
                <el-form-item label="单位">
                    <el-input placeholder="请输入单位" v-model="form.unit"></el-input>
                </el-form-item>
                <el-form-item label="数量">
                    <el-input placeholder="请输入数量" v-model="form.num"></el-input>
                </el-form-item>
                <el-form-item label="设备档案号">
                    <el-input placeholder="请输入设备档案号" v-model="form.equipFileNum"></el-input>
                </el-form-item>
                <el-form-item label="档案位置">
                    <el-input placeholder="请输入档案位置" v-model="form.fileLoca"></el-input>
                </el-form-item>
                <!-- <el-form-item label="校验日期" prop="calibDate">
                    <el-date-picker v-model="form.calibDate" type="date" placeholder="请选择校验日期">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="备注">
                    <el-input placeholder="请输入备注" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 页头区域 -->
        <el-form class="manage-header">
            <!-- 新增用户 -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary">新增设备</el-button>
            </el-form-item>

            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 10px;">批量删除</el-button>
            </el-form-item>
            <el-form-item>
            <el-upload :action="exportUrl" :show-file-list="false" accept="xlsx" :on-success="handleExcelImportSuccess"
                    :on-error="handleExcelImportFail" style="display: inline-block;">
                    <el-button style="margin-left: 30px;" class="ml-5" type="primary">导入<i
                            class="el-icon-bottom"></i></el-button>
                </el-upload>
             </el-form-item>
              <el-form-item>
                <el-button @click="exp" style="margin-left: 30px;" type="primary">导出<i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 20px;" class="download-template-btn"
                    type="text">下载导入模板</el-button>
            </el-form-item>

            <el-form-item style="margin-left: 600px;">
                <el-input placeholder="请输入设备名称" v-model="equipForm.equipName"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load" type="primary" style="margin-left: 10px;">查询</el-button>
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 主要表格 -->
        <el-table  :data="tableData" style="width: 8.0rem" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="equipStatus" label="设备状态" align="center">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.equipStatus === '合格'">合格</el-tag>
                    <el-tag type="warning" v-if="scope.row.equipStatus === '准用'">准用</el-tag>
                    <el-tag type="danger" v-if="scope.row.equipStatus === '停用'">停用</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="id" label="固定资产编号" align="center" width="150px" show-overflow-tooltip></el-table-column>
            <el-table-column prop="equipType" label="设备种类" align="center" width="150px" show-overflow-tooltip></el-table-column>
            <el-table-column prop="equipName" label="设备名称" align="center"></el-table-column>
            <el-table-column prop="department" label="所在部门" align="center"></el-table-column>
            <el-table-column prop="price" label="价格" align="center"></el-table-column>
            <el-table-column prop="model" label="型号" align="center"></el-table-column>
            <el-table-column prop="manuFact" label="制造商" align="center"></el-table-column>
            <el-table-column prop="acceptDate" label="验收日期" align="center" width="100px"></el-table-column>
            <el-table-column prop="prodDate" label="生产日期" align="center" width="100px"></el-table-column>
            <el-table-column prop="facNum" label="出厂编号" align="center" width="150px" show-overflow-tooltip></el-table-column>
            <el-table-column prop="purchCont" label="设备文档" align="center" width="160px" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-link type="primary" @click="watch(scope.row.purchContPath)">
                        {{ scope.row.purchCont }}
                    </el-link>
                </template>
            </el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
            <el-table-column prop="country" label="国别" align="center"></el-table-column>
            <el-table-column prop="unit" label="单位" align="center"></el-table-column>
            <el-table-column prop="num" label="数量" align="center"></el-table-column>
            <el-table-column prop="equipFileNum" label="设备档案号" align="center"></el-table-column>
            <el-table-column prop="fileLoca" label="档案位置" align="center"></el-table-column>
            <!-- <el-table-column prop="calibDate" label="校验日期" align="center"></el-table-column> -->
            <el-table-column label="操作" width="250" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="handleDownload(scope.row.purchContPath)">档案下载</el-button>
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>

                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            editedFormData: {},
            dialogVisible: false,
            tableData: [],
           
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            equipForm: {
                equipName: '',
                // calibDate: '',
            },
            options: [{
                value: '合格',
                label: '合格'
            }, {
                value: '准用',
                label: '准用'
            }, {
                value: '停用',
                label: '停用'
            }],

            options2: [{
                value: '房屋建筑物',
                label: '房屋建筑物'
            }, {
                value: '机器设备',
                label: '机器设备'
            }, {
                value: '仪器设备',
                label: '仪器设备'
            }, {
                value: '运输设备',
                label: '运输设备'
            }, {
                value: '办公设备',
                label: '办公设备'
            },
            ],
            form: {
                purchCont: '',
                purchContPath: '',
                selection: '',
                index: '',
                equipStatus: '',
                id: '',
                originalId: '',
                equipType: '',
                equipName: '',
                department: '',
                price: '',
                model: '',
                manuFact: '',
                acceptDate: '',
                prodDate: '',
                remarks: '',
                country: '',
                unit: '',
                num: '',
                equipFileNum: '',
                fileLoca: '',
                calibDate: '',
            },
            rules: {
                purchCont: [
                    { required: true, message: "请先上传购买合同", trigger: "submit" }
                ],
                equipStatus: [
                    { required: true, message: "请输入设备状态", trigger: "blur" }
                ],
                id: [
                    { required: true, message: "请输入固定资产编号", trigger: "blur" }
                ],
                equipType: [
                    { required: true, message: "请输入设档案", trigger: "blur" }
                ],
                equipName: [
                    { required: true, message: "请输入设备名称", trigger: "blur" }
                ],
                department: [
                    { required: true, message: "请输入所在部门", trigger: "blur" }
                ],
                price: [
                    { required: true, message: "请输入价格", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "请输入规格型号", trigger: "blur" }
                ],
                manuFact: [
                    { required: true, message: "请输入生产商", trigger: "blur" }
                ],
                acceptDate: [
                    { required: true, message: "请选择验收日期", trigger: "blur" }
                ],
                prodDate: [
                    { required: true, message: "请选择生产日期", trigger: "blur" }
                ],
                facNum: [
                    { required: true, message: "请输入出厂编号", trigger: "blur" }
                ],

                remarks: [
                    { required: true, message: "请输入备注", trigger: "blur" }
                ],
                country: [
                    { required: true, message: "请输入国别", trigger: "blur" }
                ],
                unit: [
                    { required: true, message: "请输入单位", trigger: "blur" }
                ],
                num: [
                    { required: true, message: "请输入数量", trigger: "blur" }
                ],
                equipFileNum: [
                    { required: true, message: "请输入设备档案号", trigger: "blur" }
                ],
                fileLoca: [
                    { required: true, message: "请输入档案位置", trigger: "blur" }
                ],
                calibDate: [
                    { required: true, message: "请选择校验日期", trigger: "blur" }
                ]
            }
        }
    },
    created() {
        this.load()
    },
    computed: {
        exportUrl() {
            return "http://192.168.1.14:9090/equipment/import"
        },
    },
    methods: {

        handleEdit(row) {
            console.log(row,row.id)
            this.originalId = row.id;
       
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        //导入成功加载钩子
        handleExcelImportSuccess() {
            this.$message.success("文件导入成功")
            this.load()
        },
        //导入失败加载钩子
        handleExcelImportFail() {
            this.$message.error("导入失败，请注意上传文件中的字段格式")
            this.load()
        },
        // handleEdit(row) {
        //     // this.modelType = 1
        //     // 注意需要对当前行数据进行深拷贝，否则会出错
        //     this.originalId = row.id;
        //     this.editedFormData = Object.assign({}, row);
        //     this.form = JSON.parse(JSON.stringify(row))
        //     this.dialogVisible = true

        // },
        handleDownload(path) {
            window.open(path + '#toolbar=0', '_blank')
        },
        // handleChange(file){

        //     this.form.purchCont = file.name
        //     this.$forceUpdate();
        //     // console.log( this.form.purchCont)
        // },
        handleFileUploadSuccess(res) {
            console.log(res)
            // this.form.purchCont=''
            if (res.code === "200") {
                this.form.purchCont = res.data.name
                this.form.purchContPath = res.data.path
                this.$forceUpdate();
                this.$message.success("上传成功")
                // console.log(  res.data.name)

                // this.refreshForm(); 
                // console.log(   this.form.purchCont)
            } else {
                this.$message.error("上传失败")
            }

            // this.load()
        },
        watch(path) {
            // window.open(path + '#toolbar=0', '_blank' )
            // let url = "http://127.0.0.1:8012/onlinePreview?url=" + encodeURIComponent(Base64.encodeURI(path));
            let url = "http://192.168.1.14:8012/onlinePreview?url=" + path;
            window.open(url);
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        exp() {
            // window.location.href = "http://192.168.1.14:9090/equipLedger/export";
            window.location.href = "http://192.168.1.14:9090/equipment/export" 
        },
        downloadTemplate() {
            // window.open("http://192.168.1.14:9090/file/210118f187944394ad5fc7f1cca2a028.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/5cc40c484d544068a3ecfacc18328b80.xlsx"

        },
        //提交表单
        submit() {
           
            this.$refs.formDialog.validate((valid) => {

                if (valid) {
                    if(this.originalId === this.form.id){
                        this.request.put("/equipment",this.form).then(res => {
                            this.$message.success("保存成功");
                            this.dialogVisible = false;
                            this.load();
                        })
                    }else{
                        this.request.post("/equipment", this.form).then(res => {
                
                        if (res.code === "200") {
                            console.log("originalId", this.originalId )
                        // 删除当前表单对应的数据
                        this.handleDeletee(this.originalId); 
                            this.$message.success("保存成功");
                            this.dialogVisible = false;
                            this.load();
                        }else if(res.code === "402"){
                            this.$message.error("固定资产编号已存在，请重新修改！")
                        }else {
                            this.$message.error("保存失败");
                        }
                    });
                    }
                    
                } else {
                    return false;
                }
        });
},

        handleClose() {
            this.$refs.formDialog.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //  //导出表格
        //  exp(){
        //     window.open("http://192.168.1.14:9090/equipLedger/export")
        //  },

        //  //导入成功加载钩子
        //  handleExcelImportSuccess(){
        //     this.$message.success("文件导入成功")
        //     this.load()
        //  },
        //   //导入失败加载钩子
        // handleExcelImportFail(){
        //     this.$message.error("导入失败，请注意上传文件中的字段格式")
        //     this.load()
        //  },

        //查询重置
        reset() {
            this.equipForm.name = ""
            this.load()
        },

        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipment/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleDeletee(originalId) {
    this.request.delete("/equipment/" + originalId).then(res => {
        if (res.code === '200') {
          
        }
        // 重新获取列表的接口
        this.load()
    }).catch(() => {
        this.$message({
            type: 'error',
            message: '删除失败'
        });
    });
},

        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipment/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        load() {
            this.request.get("/equipment/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.equipForm.name,
                }
            })
                .then(
                    res => {
                        // console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            this.request.post("/equipment", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },

    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>