      </el-header>
    </el-container>
    <div>
      <!-- 个人信息卡片 -->
      <el-card class="box-card">
        <h1>个人信息</h1>
        <el-form label-width="100px" ref="personalFormRef" :model="form" :rules="rules" >
          <div style="text-align: center; margin:10px 0">
            <el-upload class="avatar-uploader" action="http://192.168.1.14:9090/fileAvatar/upload"
              :show-file-list="false" :on-success="handleAvatarSuccess" :on-error="handleAvatarFail">
              <img v-if="form.imgPath" :src="form.imgPath" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </div>

          <el-row>
            <el-col :span="12">
              <el-form-item label="部门" prop="department">
                <el-input v-model="form.department"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="加入公司" style="width: 280px;" prop="enter_time">
                <el-date-picker v-model="form.enterTime" type="date" placeholder="选择日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="姓名" prop="name">
                <el-input v-model="form.name" placeholder="xyz"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="工号" prop="jobNum">
                <el-input v-model="form.jobNum" disabled></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="性别" style="width: 280px;" prop="sex">
                <el-select v-model="form.gender" placeholder="请选择用户性别">
                  <el-option label="男" value="男" />
                  <el-option label="女" value="女" />
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="生日" style="width: 280px;" prop="birth">
                <el-date-picker v-model="form.birth" type="date" placeholder="选择日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="年龄" prop="age">
                <el-input v-model="form.age"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="身份证" prop="idNum">
                <el-input v-model="form.idNum"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="专业构成" prop="professional_composition">
                <el-input v-model="form.professionalComposition"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="文化程度" prop="edu_background">
                <el-select v-model="form.eduBackground" placeholder="请选择文化程度">
                  <el-option label="初中" value="初中" />
                  <el-option label="高中" value="高中" />
                  <el-option label="大专" value="大专" />
                  <el-option label="本科" value="本科" />
                  <el-option label="硕士" value="硕士" />
                  <el-option label="博士" value="博士" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="专业" prop="major">
                <el-input v-model="form.major"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="毕业院校" prop="grad_institutions">
                <el-input v-model="form.gradInstitutions"></el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-row>
            <el-col :span="12">
              <el-form-item label="职称名称" prop="prof_title">
                <el-input v-model="form.profTitle"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="职称级别" prop="prof_quality">
                <el-select v-model="form.profQuality" placeholder="请选择职称级别">
                  <el-option label="初级" value="初级" />
                  <el-option label="中级" value="中级" />
                  <el-option label="高级" value="高级" />
                  <el-option label="正高级" value="正高级" />
                </el-select>
              </el-form-item>
