          </el-row>

          <el-row>
            <el-col :span="12">
              <el-form-item label="毕业时间" style="width: 280px;" prop="grad_time">
                <el-date-picker v-model="form.gradTime" type="date" placeholder="选择日期">
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="是否参保" prop="is_insured" style="width: 280px;">
                <el-select v-model="form.isInsured" placeholder="请选择是否参保">
                  <el-option label="是" value="是" />
                  <el-option label="否" value="否" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="是否专家" prop="professional" style="width: 280px;">
                <el-select v-model="form.professional" placeholder="请选择是否为专家">
                  <el-option label="是" value="是" />
                  <el-option label="否" value="否" />
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-button type="primary" @click="open">修改信息</el-button>
          <el-button type="danger" @click="exit">退出</el-button>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import HomeHeader from '../../../components/HomeHeader.vue';
import { validIdNum, validAge } from '../../../utils/validate';
export default {
  components: { HomeHeader },
  data() {
    const isIdNum = (rule, value, callback) => {
      validIdNum(value) ? callback() : callback(new Error('身份证输入不符合规范'))
    }
    const isAge = (rule, value, callback) => {
      validAge(value) ? callback() : callback(new Error('年龄仅支持数字填写'))
    }
    return {
      form: {
        department: '',
        name: '',
        gender: '',
        birth: '',
        age: '',
        idNum: '',
        professionalComposition: '',
        eduBackground: '',
        major: '',
        gradInstitutions: '',
        gradTime: '',
        profQuality: '',
        profTitle: '',
        isInsured: '',
        enterTime: '',
        imgPath: '',
        professional: ''
      },
      rules: {
        department: [
          { required: true, message: '部门为必填项', trigger: 'blur' }
        ],
        name: [
          { required: true, message: '姓名为必填项', trigger: 'blur' }
        ],
        jobNum: [
          { required: true, message: '工号为必填项', trigger: 'blur' }
        ],
        idNum: [
          { required: true, message: '身份证为必填项', trigger: 'blur' },
          { validator: isIdNum, trigger: 'blur' }
        ],
        age: [
          { required: true, message: '年龄为必填项', trigger: 'blur' },
          { validator: isAge, trigger: 'blur' }
        ]

      },
    }
  },
  created() {
    let user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}
    this.form.jobNum = user.id,
      this.form.name = user.loginName
    this.request.get("/userInfo/" + user.id).then(res => {
      console.log(res)
      if (res.code === '200') {
        this.form = res.data;
        this.form.jobNum = user.id,
          this.form.name = user.loginName
      }
    })
  },
  methods: {
    exit() {
      this.$router.push({ path: '/home' })
    },
    load() {
      let user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}

      this.request.get("/userInfo/" + user.id).then(res => {
        console.log(res)
        if (res.code === '200') {
          this.form = res.data;

        }
      })
    },
    open() {
      this.$refs.personalFormRef.validate((valid) => {
        if (valid) {
          this.$confirm('此操作将修改个人信息, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.request.post("/userInfo", this.form).then(res => {
              console.log(res)
              if (res.code == '200') {

                this.$message({
                  type: 'success',
                  message: '修改成功!'
                })
              } else {
                this.$message({
                  type: 'error',
                  message: '修改失败！'
                })
              }
              this.load();
            })
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消修改'
            });
            this.load();
          });
        } else {
          return false;
        }
      })
    },
    handleAvatarSuccess(res) {
      if (res) {
        console.log(res)
        this.form.imgPath = res;
      } else {
        console.error('无法获取有效的图片 URL');
      }
    },
    handleAvatarFail() {
      this.$message({
        type: 'error',
        message: '上传失败，请勿上传相同头像'
      });
    }
  },
}
</script>

