<template>
    <div class="manage">
        <!-- 表头 -->
        <el-form class="manage-header">
            <!-- 创建项目 -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary">创建项目</el-button>
            </el-form-item>
            <!-- 批量删除 -->
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 10px;">批量删除</el-button>
            </el-form-item>
            <!-- 查询表单 -->
            <el-form-item style="margin-left: 50vh;">
                <el-input placeholder="请输入项目名称" v-model="projectForm.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load" type="primary">查询</el-button>
            </el-form-item>
            <el-form-item style="margin-left: 10px;">
                <el-input placeholder="请输入负责人" v-model="projectForm.manager"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load" type="primary">查询</el-button>
            </el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset" style="margin-left:10px">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 主要表格 -->
        <el-table height="90%" :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="name" label="项目名" align="center"></el-table-column>
            <el-table-column prop="manager" label="项目管理员" align="center"></el-table-column>
            <el-table-column label="操作" width="500" align="center">
                <template slot-scope="scope">
                    <el-button type="success" style="width: 0.4rem;"  size="mini" @click="top_to_project(scope.row)">{{getButtonLabel(scope.row.level)}}</el-button>
                    <el-button style="margin-left:20px" size="mini" type="primary" @click="fileLoad2(scope.row.id, scope.row.name)">文件管理
                    </el-button>
                    <el-button style="margin-left:20px" size="mini" type="primary"
                        @click="userLoad2(scope.row.id, scope.row.name)">
                        成员管理</el-button>
                    <el-button style="margin-left:20px" size="mini" type="primary"  @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button style="margin-left:20px" size="mini" type="danger" @click="handleDelete(scope.row.id)">
                        删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-dialog title="提示" :visible.sync="updateDialogVisible" width="50%" :before-close="updateCancel">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="90px">
                <el-form-item label="项目名称" prop="name">
                    <el-input placeholder="请输入项目名" v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="负责人" prop="manager">
                    <el-input placeholder="请输入负责人" v-model="form.manager"></el-input>
                </el-form-item>
            </el-form>

            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="updateCancel">取 消</el-button>
                <el-button type="primary" @click="submitUpdate">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
        <!-- 创建项目弹窗 -->
        <el-dialog title="项目创建" :visible.sync="ProAddDialogVisible" width="30%">
            <!-- 创建项目表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="90px">
                <el-form-item label="项目名称" prop="name">
                    <el-input placeholder="请输入项目名" v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="负责人" prop="manager">
                    <el-input placeholder="请输入负责人" v-model="form.manager"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 文件管理弹窗 -->
        <el-dialog title="文件管理" :visible.sync="FileDialogVisible" width="50%">
            <div>
                <el-button icon="el-icon-circle-plus" circle @click="addFileLoad()"></el-button>
                <el-form :inline="true" :model="fileForm" style="margin-left: 90px;">
                    <el-form-item>
                        <el-input placeholder="请输入文件编号" v-model="fileForm.fileNum"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="fileLoad" type="primary">查询</el-button>
                    </el-form-item>
                    <el-form-item>
                        <el-input placeholder="请输入文件名" v-model="fileForm.fileName"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="fileLoad" type="primary">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <el-table :data="fileData" >
                <el-table-column prop="fileNum" label="文件编号" width="300rem" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="fileName" label="文件名" width="250rem" show-overflow-tooltip>
                </el-table-column>
                <el-table-column label="操作" width="200" align="center">
                    <template slot-scope="scope">
                        <el-button style="margin-left:5px" size="mini" type="danger" @click="fileDelete(scope.row.id)">
                            删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <div style="padding: 10px 0">
                    <el-pagination @size-change="fileSizeChange" @current-change="fileCurrentChange"
                        :current-page="filePageNum" :page-sizes="[2, 5, 10, 20]" :page-size="filePageSize"
                        layout="total, sizes, prev, pager, next, jumper" :total="filetotal">
                    </el-pagination>
                </div>
            </span>
        </el-dialog>
        <!-- 成员管理弹窗 -->
        <el-dialog title="成员管理" :visible.sync="UserDialogVisible" width="30%">
            <el-button icon="el-icon-circle-plus" circle @click="addUserLoad()"></el-button>
            <el-form :inline="true" :model="userForm" style="margin-left: 30px;">
                <el-form-item>
                    <el-input placeholder="请输入工号" v-model="userForm.userId" style="width:13vh;"></el-input>
                    <el-input placeholder="请输入姓名" v-model="userForm.loginName"
                        style="width:13vh;margin-left: 10px;"></el-input>
                    <el-button @click="userLoad" type="primary" style="margin-left: 10px;">查询</el-button>
                    <el-button type="warning" @click="userFormReset">重置</el-button>
                </el-form-item>




                <!-- <el-form-item>
                    <el-input placeholder="请输入工号" v-model="userForm.userId"></el-input>
                </el-form-item> -->
                <!-- <el-form-item>
                    <el-button @click="userLoad" type="primary">查询</el-button>
                </el-form-item> -->
            </el-form>
            <el-table :data="userData" style="width: 100%">
                <el-table-column prop="userId" label="工号">
                </el-table-column>
                <el-table-column prop="loginName" label="姓名">
                </el-table-column>

                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button style="margin-left:5px" size="mini" type="danger" @click="userDelete(scope.row.id)">
                            删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <div style="padding: 10px 0">
                    <el-pagination @size-change="userSizeChange" @current-change="userCurrentChange"
                        :current-page="userPageNum" :page-sizes="[2, 5, 10, 20]" :page-size="userPageSize"
                        layout="total, sizes, prev, pager, next, jumper" :total="usertotal">
                    </el-pagination>
                </div>
            </span>
        </el-dialog>
        <!-- 新增文件弹窗 -->
        <el-dialog title="添加文件" :visible.sync="FileAddDialogVisible" width="50%">
            <el-form :inline="true" :model="addFileForm" style="margin-left: 90px;">
                <el-form-item>
                    <el-input placeholder="请输入文件编号" v-model="addFileForm.num"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="addFileLoad" type="primary">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-input placeholder="请输入文件名" v-model="addFileForm.name"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="addFileLoad" type="primary">查询</el-button>
                </el-form-item>
            </el-form>
            <el-table :data="addFileData" style="width: 100%">
                <el-table-column prop="num" label="文件编号" width="300rem" show-overflow-tooltip>
                </el-table-column>
                <el-table-column prop="name" label="文件名" width="250rem" show-overflow-tooltip>
                </el-table-column>
                <!-- <el-table-column prop="path" width="0">
                </el-table-column> -->

                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button style="margin-left:5px" size="mini" type="danger"
                            @click="addFile(scope.row.num, scope.row.name, scope.row.path,scope.row.downloadpath)">添加</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <div style="padding: 10px 0">
                    <el-pagination @size-change="addFileSizeChange" @current-change="addFileCurrentChange"
                        :current-page="addFilePageNum" :page-sizes="[2, 5, 10, 20]" :page-size="addFilePageSize"
                        layout="total, sizes, prev, pager, next, jumper" :total="addFileTotal">
                    </el-pagination>
                </div>
            </span>
        </el-dialog>
        <!-- 成员增加弹窗 -->
        <el-dialog title="添加成员" :visible.sync="UserAddDialogVisible" width="30%">
            <el-form :inline="true" :model="addUserForm" style="margin-left: 40px;">
                <el-form-item>
                    <el-input placeholder="请输入工号" v-model="addUserForm.userId" style="width:130px;"></el-input>
                    <el-input placeholder="请输入姓名" v-model="addUserForm.loginName"
                        style="width:130px; margin-left: 10px;"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="addUserLoad" type="primary">查询</el-button>
                    <el-button type="warning" @click="addUserFormReset">重置</el-button>

                </el-form-item>
                <!-- <el-form-item>
                    <el-input placeholder="请输入成员账户" v-model="addUserForm.loginName"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="addUserLoad" type="primary">查询</el-button>
                </el-form-item>
                <el-form-item>
                    <el-input placeholder="请输入成员姓名" v-model="addUserForm.name"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button @click="addUserLoad" type="primary">查询</el-button>
                </el-form-item> -->
            </el-form>
            <el-table :data="addUserData" style="width: 100%">
                <el-table-column prop="id" label="工号">
                </el-table-column>
                <el-table-column prop="loginName" label="成员账户">
                </el-table-column>

                <el-table-column label="操作" align="center">
                    <template slot-scope="scope">
                        <el-button style="margin-left:5px" size="mini" type="danger"
                            @click="addUser(scope.row.loginName, scope.row.id)">添加</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <div style="padding: 10px 0">
                    <el-pagination @size-change="addUserSizeChange" @current-change="addUserCurrentChange"
                        :current-page="addUserPageNum" :page-sizes="[2, 5, 10, 20]" :page-size="addUserPageSize"
                        layout="total, sizes, prev, pager, next, jumper" :total="addUserTotal">
                    </el-pagination>
                </div>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            fileData: [],
            addFileData: [],
            userData: [],
            addUserData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],

            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            filetotal: 0,
            filePageNum: 1,
            filePageSize: 10,
            usertotal: 0,
            userPageNum: 1,
            userPageSize: 10,
            addFileTotal: 0,
            addFilePageNum: 1,
            addFilePageSize: 10,
            addUserTotal: 0,
            addUserPageNum: 1,
            addUserPageSize: 10,
            projectForm: {
                name: '',
                manager: '',
            },
            FileDialogVisible: false,
            UserDialogVisible: false,
            FileAddDialogVisible: false,
            UserAddDialogVisible: false,
            ProAddDialogVisible: false,
            updateDialogVisible: false,
            rules: {
                name: [
                    { required: true, message: "请输入项目名称", trigger: "blur" }
                ],
                manager: [
                    { required: true, message: "请输入项目负责人", trigger: "blur" }
                ],
            },
            form: {
                id: '',
                name: "",
                manager: "",
            },
            userForm: {
                loginName: "",
                userId: "",
            },
            fileForm: {
                num: "",
                name: "",
            },
            addFileForm: {
                num: '',
                name: '',
            },
            addUserForm: {
                loginName: "",
                userId: "",
            },
            role: "",
            id: "",
            name: "",
            fileNum: "",
            fileName: "",
            projectList: [],
            row:{
                id:null,
                level:null
            },
        }
    },
    created() {
        this.request.get("/project").then(res => {
            this.projectList = [...res.data].reverse();
            this.total = res.data.total;
        });
        this.load();
    },
    methods: {
        reset() {
            this.projectForm.name = ""
            this.projectForm.manager = ""
            this.load()
        },
        //查询重置
        userFormReset() {

            this.userForm.loginName = ""
            this.userForm.userId = ""
            this.userLoad2(this.id, this.name)
        },
        addUserFormReset() {

            this.addUserForm.loginName = ""
            this.addUserForm.userId = ""
            this.addUserLoad()
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        //提交用户表单 projectFormRef
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/project", this.form).then(res => {   //res为后台返回的结果
                        if (res.code === "200") {
                            this.$message.success("保存成功")
                            this.ProAddDialogVisible = false
                            this.load()
                        } else {
                            this.$message.error("保存失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
          //编辑用户表单
          submitUpdate() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.put("/project", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("修改成功")
                            this.updateDialogVisible = false
                            this.load()
                        } else {

                            this.$message.error("修改失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        //弹窗关闭时
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        
        updateCancel() {
            this.updateDialogVisible = false
        },
        handleEdit(row) {
            this.form.role
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            // this.dialogVisible = true
            this.updateDialogVisible = true

        },
        addFile(num, name, path, downloadpath) {
            console.log(downloadpath)
            this.request.get("/projectFile/add?projectId=" + this.id + "&projectName=" + this.name + "&fileNum=" + num + "&fileName=" + name + "&path=" + path + "&downloadPath=" + downloadpath).then(res => {   //res为后台返回的结果
                if (res.code === "200") {
                    this.$message.success("保存成功")
                    // this.dialogVisible = false
                    this.fileLoad2(this.id, this.name)
                } else if (res.code === "402") {
                    this.$message.error("数据已存在")
                }
            })
        },
        addUser(loginName, userId) {
            this.request.get("/projectUser/add?projectId=" + this.id + "&projectName=" + this.name + "&loginName=" + loginName + "&userId=" + userId).then(res => {   //res为后台返回的结果
                if (res.code === "200") {
                    this.$message.success("保存成功")
                    // this.UserDialogVisible = false
                    this.userLoad2(this.id, this.name)
                } else if (res.code === "402") {
                    this.$message.error("数据已存在")
                }
            })
        },
        // 弹窗关闭时
        handleClose() {
            this.$refs.form.resetFields()
            this.ProAddDialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        handleAdd() {
            this.modelType = 0
            this.ProAddDialogVisible = true  //弹窗
            this.form = {}
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该项目, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/project/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        fileDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/projectFile/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.fileLoad()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        userDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/projectUser/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.userLoad()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/project/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        load() {
            this.request.get("/project/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.projectForm.name,
                    manager: this.projectForm.manager
                }
            }).then(res => {
                console.log(res)
                this.tableData = res.data.records
                console.log('this.tableData',this.tableData)
                this.total = res.data.total
            })
            // fetch("http://localhost:9090/project/page?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize + "&name=" + this.projectForm.name + "&manager=" + this.projectForm.manager)
            //     .then(res => res.json()).then(
            //         res => {
            //             console.log(res)
            //             this.tableData = res.data.records
            //             this.total = res.data.total
            //         }
            //     )
        },
        top_to_project(row){
            console.log('row:',row)
            console.log("row.level:",row.level)
            const form = {
                id:row.id,
                level: Number(row.level) === 1 ? 0 : 1,  // 如果是置顶状态，level为1；否则为0
            }
            console.log("form:",form)
            try{
                this.request.post("/project/updateLevel", form).then(() => {
                    // 更新成功后手动更新 row 的 level 属性
                    this.$set(row, 'level', form.level);
                });
            }catch (error) {
                console.error('发生错误:', error);
            }
        },
        getButtonLabel(row){
            return Number(row) === 1 ? '置顶' : '取消置顶';
        },
        fileLoad() {
            this.request.get("/projectFile/pager", {
                params: {
                    pageNum: this.filePageNum,
                    pageSize: this.filePageSize,
                    projectId: this.id,
                    fileNum: this.fileForm.fileNum,
                    fileName: this.fileForm.fileName
                }
            }).then(res => {

                this.fileData = res.data.records
                this.filetotal = res.data.total

            })
