<template>
  <div>
    <!-- 页头区域 -->
    <el-container>
      <el-header class="el-header">
        <home-header />
      </el-header>
    </el-container>
    <!-- 编辑的表单信息 -->
    <el-dialog title="提示" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
      <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
        <el-form-item label="用餐日期" prop="mealDate">
          <el-date-picker v-model="form.mealDate" type="date">
          </el-date-picker>
        </el-form-item>
        <!-- <el-form-item label="用餐时间" prop="mealTime">
          <el-input v-model="form.mealTime">
          </el-input>
        </el-form-item> -->
        <el-form-item prop="mealTime">
            <el-time-select v-model="form.mealTime" :picker-options="{
              start: '08:30',
              step: '00:30',
              end: '22:30'
            }" placeholder="选择时间">
            </el-time-select>
        </el-form-item>
        <el-form-item label="用餐人数" prop="orderNum">
          <el-input v-model="form.orderNum"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 左侧导航栏 -->
    <div>
      <div class="box-card">
        <h1>客餐预约</h1>
      </div>
      <div id="handStyle" class="box-card2">
        <h2 @click="tofoodorder">立即预约</h2>
      </div>
      <div class="box-card3">
        <h2>预约记录</h2>
      </div>
    </div>
    <!-- 卡片区域 -->
    <div>
      <el-card style="width: 50%; left: 82vh;" class="table" :header-cell-style="{ color: 'green', fontSize: '24px' }">
        <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/foodorderhome/myorder' }">立即预约</el-breadcrumb-item>
            
                    </el-breadcrumb>
        <h1>我的预约</h1>
        <el-table height="845px" :data="tableData" style="width: 100%" border stripe
          @selection-change="handleSelectionChange">
          <el-table-column prop="mealDate" label="用餐日期" align="center" width="180"></el-table-column>
          <el-table-column prop="mealTime" label="用餐时间" align="center" width="180"></el-table-column>
          <el-table-column prop="orderNum" label="用餐人数" align="center" width="180"></el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button size="mini" type="primary" @click="handleEdit(scope.row)" :disabled="isDateBeforeToday(scope.row.mealDate,scope.row.mealTime)">编辑</el-button>
              <el-popconfirm class="ml-5" confirm-button-text="确定" cancel-button-text="我再想想" ican="el-icon-info"
                icon-color="red" title="您确定删除吗？" @confirm="del(scope.row.id)">
              </el-popconfirm>
              <el-button style="margin-left: 5px" size="mini" type="danger" @click="handleDelete(scope.row.id)" :disabled="isDateBeforeToday(scope.row.mealDate,scope.row.mealTime)">取消预约
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </div>
    <!-- 页脚区域 -->
    <!-- <el-container class="footer">
      <el-footer class="el-footer">
        <home-footer />
      </el-footer>
    </el-container> -->
    <!-- 页码区域 -->
    <div class="page">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[2, 5, 15, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
export default {
  components: { HomeHeader, HomeFooter },
  data() {
    return {
      tableData: [],
      rules: {
          //验证用户名是否合法
          mealDate: [
            { required: true, message: '请选择用餐日期' },
          ],
          mealTime: [
            { required: true, message: '请选择用餐时间' },
          ],
          orderNum: [
            { required: true, message: '请填写用餐人数' },
          ]
        },
      dialogVisible: false,
      pageNum: 1,
      pageSize: 15,
      total: 0,
      
      form: [],
      user: localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {},
    };
  },
  created() {
    this.load()
  },
  methods: {
   //判断就餐日期是否在当前日期之前
   isDateBeforeToday(date,time) {
      const mealDateTime = new Date(`${date} ${time}`);
      const now = new Date();
      return mealDateTime <= now;
    },
    load() {
      this.request.get("/order/pageRecord?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize+"&orderUser="+ this.user.loginName )
        .then(
          res => {
            console.log(res)
            this.tableData = res.data.records
            this.total = res.data.total
            

          }
        )
    },
    submit() {
      this.$refs.form.validate((valid)=>{
            if(valid){
      this.request.post("/order", this.form).then((res) => {
        //res为后台返回的结果
        if (res.code === "200") {
          this.$message.success("修改成功");
          this.dialogVisible = false;
          this.load();
        } else {
          this.$message.error("操作失败");
        }
      })}else{
          return false;                    
       }
       })
    },
    cancel() {
      // this.handleClose()
      this.dialogVisible = false;
    },
    handleSelectionChange(val) {
    
      this.multipleSelection = val;
    },
    handleCurrentChange(pageNum) {
      this.pageNum = pageNum;
      this.load();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.load();
    },
    handleClose(key, keyPath) {
      this.dialogVisible = false;
      // console.log(key, keyPath);
    },
    handleEdit(row) {
      this.modalType = 1;
      // 注意需要对当前行数据进行深拷贝，否则会出错
      this.form = JSON.parse(JSON.stringify(row));
      this.dialogVisible = true;
    },
    handleDelete(id) {
      this.$confirm("是否确定取消预约", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.request.delete("/order/" + id).then((res) => {
            if (res.code === "200") {
              this.$message({
                type: "success",
                message: "取消预约成功!",
              });
            }else{
              this.$message({
                type: "warning",
                message: "距离就餐时间小于三十分钟，不可取消预约操作!",
              });
            }

            // 重新获取列表的接口
            this.load();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消操作",
          });
        });
    },
    tofoodorder() {
      this.$router.push({ path: '/foodorderhome' })
    },
  },
};
</script>

<style lang="less" scoped>
.el-header {
  padding: 0px;
}

.el-footer {
  padding: 0px;
}

.box-card {
  position: absolute;
  top: 250px;
  left: 9%;
  width: 21%;
  height: 100px;
  text-align: center;
  margin: auto;
  color: #f2f2f2;
  background: linear-gradient(-135deg, transparent 20px, #01743b 0);
}

.box-card2 {
  position: absolute;
  top: 350px;
  left: 9%;
  width: 21%;
  height: 70px;
  text-align: center;
  color: #02A7F0;
  background-color: #f2f2f2;
}

.box-card3 {
  position: absolute;
  top: 420px;
  left: 9%;
  width: 21%;
  height: 70p;
  text-align: center;
  color: #02A7F0;
  background-color: #f2f2f2;
}

.table {
  position: absolute;
  top: 250px;
  left: 700px;
  width: 800px;
  height: 1000px;
  text-align: center;
  // color: #02a7f0;
}

.page {
  padding: 10px 0;
  position: absolute;
  top: 1250px;
  left: 700px;
}

element.style {
  height: 0px;
}
</style>