<template>
  <div>
    <!-- header页头 -->
    <el-container>
      <el-header class="el-header">
        <home-header />
      </el-header>
    </el-container>
    <!-- 侧边栏 -->
    <div>
      <div class="box-c"></div>
      <div id="handStyle" class="box-card2">
        <h2 @click="news">公告中心</h2>
      </div>
    </div>
    <!-- 新闻展示区 -->
    <div style="width: 4.8rem;">
      <el-table :data="tableData" style="width:20rem" class="table" height="3rem"
        :header-cell-style="{ color: 'green', fontSize: '1.5em' }" width="1008rem">
        <el-table-column prop="theme" label="最新公告" width="808rem" style="width: 8rem;">
          <template slot-scope="scope">
            <el-link @click="handleDetail(scope.row.theme, scope.row.content)" show-overflow-tooltip>{{ scope.row.theme }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column prop="createTime" width="100%">
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="文章信息" :visible.sync="viewDialogVis" width="50%">
      <el-card>
        <div>
          <mavon-editor class="md" ref="mdedit" :value="content" :subfield="false" :defaultOpen="'preview'"
            :toolbarsFlag="false" :editable="false" :scrollStyle="true" :ishljs="true" />
        </div>
      </el-card>
    </el-dialog>
    <!-- footer页脚 -->
    <!-- <el-container class="footer">
      <el-footer class="el-footer">
        <home-footer />
      </el-footer>
    </el-container> -->
    <!-- 页码 -->
    <div class="page">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
        :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
export default {
  components: { HomeHeader, HomeFooter, mavonEditor },
  data() {
    return {
      tableData: [],
      pageNum: 1,
      pageSize: 10,
      total: 0,
      viewDialogVis: false,
      content: "",
    }
  },
  created() {
    this.load()
  },
  methods: {
    handleDetail(theme, content) {
      this.$router.push({ path: '/newsdetails' })
      // console.log('当前被点击的theme=' + theme);
      localStorage.setItem("theme", JSON.stringify(theme));
      localStorage.setItem("content", JSON.stringify(content));
    },
    handleCurrentChange(pageNum) {
      this.pageNum = pageNum
      this.load()
    },
    handlenewsUploadSuccess() {
      this.$message.success("上传成功")
      this.load()
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize
      this.load()
    },
    load() {
      this.request.get("/news/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize
        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
      })
      // fetch("http://localhost:9090/news/page?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize + "&name=")
      //   .then(res => res.json()).then(
      //     res => {
      //       this.tableData = res.data.records
      //       this.total = res.data.total
      //     }
      //   )
    },
    news() {
      this.$router.push({ path: '/news' })
    },
    view(content) {
      this.content = content
      this.viewDialogVis = true
    },
  }
}
</script>

