export const setRoutes = () =>{
  // const storeMenus = localStorage.getItem("backMenus")
  const storeMenus = sessionStorage.getItem("backMenus")

  if(storeMenus){
    //拼装动态路由，
      const manageRoute  =  { path: "/backend", name:'backend',component: BackEnd , redirect:'/backend/backhome',
      children:[]
    }
    
    //获取当前的路由对象名称数组
    const currentRouteNames = router.getRoutes().map(v => v.name)
    if(!currentRouteNames.includes('backend')){
        //动态添加到现在的路由对象中去
        const menus = JSON.parse(storeMenus)
    menus.forEach(item=>{
      if(item.path) //当且仅当path部位空的时候才去设置路由
      {
        //TODO：修改compoent
        // let itemMenu = { path: item.path.replace("/",""), name:item.name,component:item.component }
        let itemMenu = { path: item.path.replace("/",""), name:item.name,component:()=>import('../views/Back/'+item.component +'.vue') }

        manageRoute.children.push(itemMenu)
      }else if(item.children.length){
          item.children.forEach(itemChild=>{
            if(itemChild.path){
              // let itemMenuChild = { path: itemChild.path.replace("/",""), name:itemChild.name, component:itemChild.component }
              let itemMenuChild = { path: itemChild.path.replace("/",""), name:itemChild.name, component:()=>import('../views/Back/'+itemChild.component +'.vue') }

              manageRoute.children.push(itemMenuChild)
            }
           
          })
      }

    
    })
    router.addRoute(manageRoute)
    console.log(router)
    }
  

  }
}
setRoutes()
