<template>
    <div class="manage">
        <!-- 批量删除 -->
        <el-form class="manage-header">
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    批量删除
                </el-button>
            </el-form-item>
        </el-form>
        <!-- 主要表格 -->
        <el-table :row-style="{ height: '55px' }" :data="tableData" style="width: 1240px;" border
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="70px"  align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="70px" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="requestIp" label="操作IP" width="300px" align="center"></el-table-column>
            <el-table-column prop="username" label="操作人" width="199px" align="center"></el-table-column>
            <el-table-column prop="description" label="操作内容" width="400px" align="center" show-overflow-tooltip></el-table-column>
            <!-- <el-table-column prop="userId" label="操作人id" width="100px" align="center"></el-table-column> -->
            <el-table-column prop="actiondate" label="操作时间" width="200px"  align="center" ></el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            userForm: {
                name: '',
            }
        }
    },
    created() {
        this.load()
    },
    methods: {
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        //提交用户表单
        submit() {
            this.request.post("/operation", this.form).then(res => {   //res为后台返回的结果
                if (res.code === "200") {
                    this.$message.success("保存成功")
                    this.dialogVisible = false
                    this.load()
                } else {
                    this.$message.error("保存失败")
                }
            })
        },
        //弹窗关闭时
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        handleEdit(row) {
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }
            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/operation/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        handleAdd() {

            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}

        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/operation/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total

            })

            // fetch("http://localhost:9090/operation/page?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize)
            //     .then(res => res.json()).then(
            //         res => {
            //             console.log(res)
            //             this.tableData = res.data.records
            //             this.total = res.data.total
            //         }
            //     )
        },
        handeleSizeChange(pageSize) {
            console.log("pageSize:" + pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        download(filePath) {
            window.open(filePath)
        },
        changeEnable(row) {
            console.log("1111")
            this.request.post("/record", row).then(res => {
                if (res.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>