<script>
import HomeHeader from '../components/HomeHeader.vue';
import HomeFooter from '../components/HomeFooter.vue'
import { MessageBox } from 'element-ui';
export default {
  components: { HomeHeader, HomeFooter },
  data() {
    return {
      num: 0,
      timer: '',
      type:'',
      dataList: [
        { id: 1, src: require('../assets/1.png') },
        { id: 2, src: require('../assets/2.png') },
        { id: 3, src: require('../assets/3.png') },
        { id: 4, src: require('../assets/4.png') },
        { id: 5, src: require('../assets/5.png') },
      ],
      tableData: [],
      tableData2: [],
      projectList:[],
    }
  },
  mounted: function () {
    this.timer = setInterval(this.rotationChart, 3000)
    

  },
  created() {
    this.request.get("/project").then(res => {
            this.projectList = res.data;
            this.projectList.reverse();
            this.projectList.sort((a, b) => a.level - b.level);
            // console.log("this.projectList1:",this.projectList)
        });
      
    this.load()
    this.load2()
    //通知有多少没有审批
    this.noticeAudit()
  },
  methods: {
    //通知审批
    noticeAudit(){
      let msg = '';
      //只通知一次
     let alertShown = sessionStorage.getItem("alertShown") === "true";
      const user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {};
      if(user.role === 'EMC中心预约管理员'){
        this.type= 'second'
      }
      if(user.role === '安规中心预约管理员'){
        this.type = 'third'
      }
      this.request.get("/equipmentOrder/audit", {
                params: {              
                type: this.type,
                }
            }).then(res => {
              const pendingOrdersCount = res.data.length;
                msg = `请注意还有${pendingOrdersCount}条设备/场地预约信息需要审批`;
          
            if(res.data.length !== 0 && user.role === "超级管理员" && !alertShown){

             
            
              
                MessageBox.alert(msg,'提醒',{confirmButtonText:'确定'})
              
                sessionStorage.setItem("alertShown", "true");
            } else if(res.data.length !== 0 && (user.role === "安规中心预约管理员" || user.role === "EMC中心预约管理员") && !alertShown){
            
              MessageBox.alert(msg,'提醒',{confirmButtonText:'确定'})
              sessionStorage.setItem("alertShown", "true");
            }
             
          
        });
    },
    handleDetail(theme, content) {
      this.$router.push({ path: '/newsdetails' })
      console.log('当前被点击的theme=' + theme);
      localStorage.setItem("theme", JSON.stringify(theme));
      localStorage.setItem("content", JSON.stringify(content));
    },
    viewDetail() {
      this.$message.success("保存成功")
    },
    load() {
      this.request.get("/news/top5")
        .then(
          res => {
            // console.log(res)
            this.tableData = res.data
          }
        )
    },
    load2() {
      this.request.get("/news/mid4")
        .then(
          res => {
            this.tableData2 = res.data
          }
        )
    },
    news() {
      this.$router.push({ path: '/news' })
    },
    myproject() {
      localStorage.setItem("projectId", this.projectList[0].id),
      this.$router.push({ path: '/myproject' })
    },
    FoodOrder() {
      this.$router.push({ path: '/foodorderhome' })
    },
    DeviceOrder() {
      this.$router.push({ path: '/deviceorderhome' })
    },
    handleSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        // 返回一个对象，表示需要合并的行数和列数
        return {
          rowspan: 1, // 合并1行
          colspan: 1, // 合并2列

        };
      }
    },
    deviceHome() {
      this.$router.push({ path: '/devicehome' })
    }
  },
}
</script>

