<template>
    <div>
      <!-- 页头区域 -->
      <el-container>
        <el-header class="el-header">
          <home-header />
        </el-header>
      </el-container>
     
      <!-- 左侧导航栏 -->
    <div>
      <div class="box-c"></div>
      <div id="handStyle" class="box-card2">
        <h2 @click="devicelist">设备台帐</h2>
      </div>
      <div class="box-card3">
        <h2 @click="deviceinspplan">设备送检计划</h2>
      </div>
      <div class="box-card4">
        <h2 @click="periodverificationplanf">期间核查计划</h2>
      </div>
      <div class="box-card10">
        <h2 @click=" FixedAssets">固定资产</h2>
      </div>
    </div>
      <!-- 卡片区域 -->
  
      <div>
        <el-card   class="table" :header-cell-style="{ color: 'green', fontSize: '24px' }">
          <el-breadcrumb separator-class="el-icon-arrow-right">
                        <el-breadcrumb-item :to="{ path: '/home' }" style="font-size: 0.09rem;">首页</el-breadcrumb-item>
                        <el-breadcrumb-item :to="{ path: '/devicehome/DeviceInspectPlan' }" style="font-size: 0.09rem;">固定资产</el-breadcrumb-item>
      
                    </el-breadcrumb>
          <h1>固定资产</h1>
          <el-form class="manage-header">
            <!-- 新增用户 -->
         
            <el-form-item style="margin-left: 3.3rem;">
                <el-input placeholder="请输入设备名称" v-model="equipForm.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load" type="primary" style="margin-left: 10px;">查询</el-button>
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
       
          <!-- 主要表格 -->
          <el-table :data="tableData"  border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="equipStatus" label="设备状态" align="center"></el-table-column>
            <el-table-column prop="id" label="固定资产编号" align="center"></el-table-column>
            <el-table-column prop="equipType" label="设备种类" align="center"></el-table-column>
            <el-table-column prop="name" label="设备名称" align="center"></el-table-column>
            <el-table-column prop="department" label="所在部门" align="center"></el-table-column>
            <el-table-column prop="price" label="价格" align="center"></el-table-column>
            <el-table-column prop="specific1" label="规格型号" align="center"></el-table-column>
            <el-table-column prop="manuFact" label="生产商" align="center"></el-table-column>
            <el-table-column prop="acceptDate" label="验收日期" align="center" ></el-table-column>
            <el-table-column prop="prodDate" label="生产日期" align="center" ></el-table-column>
            <el-table-column prop="serialNum" label="出厂编号" align="center"></el-table-column>
            <el-table-column prop="purchCont" label="设备文档" align="center"  show-overflow-tooltip>
             <template slot-scope="scope">
                <el-link type="primary"  @click="handleDownload(scope.row.purchContPath)" >
                {{ scope.row.purchCont }}
                </el-link>
             </template>
            </el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
            <el-table-column prop="country" label="国别" align="center"></el-table-column>
            <el-table-column prop="unit" label="单位" align="center"></el-table-column>
            <el-table-column prop="num" label="数量" align="center"></el-table-column>
            <el-table-column prop="equipFileNum" label="设备档案号" align="center"></el-table-column>
            <el-table-column prop="fileLoca" label="档案位置" align="center"></el-table-column>
            <!-- <el-table-column prop="calibDate" label="校验日期" align="center"></el-table-column> -->
            <!-- <el-table-column label="操作" width="200" align="center" fixed="right">
                <template slot-scope="scope">
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column> -->
        </el-table>
          </el-card>
        </div>
        <!-- 页脚区域 -->
        <!-- <el-container class="footer">
          <el-footer class="el-footer">
            <home-footer />
          </el-footer>
        </el-container> -->
        <!-- 页码区域 -->
        <div class="page">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
            :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
      </div>
    </template>
    
    <script>
    import HomeHeader from "../../../components/HomeHeader.vue";
    import HomeFooter from "../../../components/HomeFooter.vue";
    export default {
      components: { HomeHeader, HomeFooter },
      data() {
          return {
            dialogVisible: false,
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            equipForm: {
                name: '',
                // calibDate: '',
            },
          
           
          }
      },
      created() {
          this.load()
      },
      methods: {
        handleDownload(path){
            let url = "http://192.168.1.14:8012/onlinePreview?url=" + path;
            window.open(url+ '#toolbar=0', '_blank');
        },
        // handleChange(file){
           
        //     this.form.purchCont = file.name
        //     this.$forceUpdate();
        //     // console.log( this.form.purchCont)
        // },
        handleFileUploadSuccess(res) {
            console.log(res)
            // this.form.purchCont=''
            if(res.code === "200" ){
                this.form.purchCont = res.data.name
                this.form.purchContPath = res.data.path
                this.$forceUpdate();
                this.$message.success("上传成功")
                // console.log(  res.data.name)
               
                // this.refreshForm(); 
                // console.log(   this.form.purchCont)
            }else{
                this.$message.error("上传失败")
            }
          
            // this.load()
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        //提交表单
        submit() {
            this.$refs.formDialog.validate((valid)=>{
                if(valid){
            this.request.post("/equipment", this.form).then(res => {
                if (res.code === "200") {
                    this.$message.success("保存成功")
                    this.dialogVisible = false
                    this.load()
                } else {
                    this.$message.error("保存失败")
                }
            })
        }else{
                return false;
            }
        })
        },
        handleClose() {
            this.$refs.formDialog.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //  //导出表格
        //  exp(){
        //     window.open("http://192.168.0.153:9090/equipLedger/export")
        //  },

        //  //导入成功加载钩子
        //  handleExcelImportSuccess(){
        //     this.$message.success("文件导入成功")
        //     this.load()
        //  },
        //   //导入失败加载钩子
        // handleExcelImportFail(){
        //     this.$message.error("导入失败，请注意上传文件中的字段格式")
        //     this.load()
        //  },

        //查询重置
        reset() {
            this.equipForm.name = ""
            this.load()
        },
        handleEdit(row) {
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipment/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipment/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        load() {
            this.request.get("/equipment/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.equipForm.name,
                }
            })
                .then(
                    res => {
                        // console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {      
            this.request.post("/equipment", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
        devicelist() {
          this.$router.push({ path: '/devicehome/devicelist' })
        },
        periodverificationplanf(){
          this.$router.push({ path: '/devicehome/periodverificationplanf' })
        },
        FixedAssets(){
        this.$router.push({ path: '/devicehome/FixedAssets' })
      },
      deviceinspplan() {
      this.$router.push({ path: '/devicehome/deviceinspplan' })
    },
      },
    };
    </script>
    
