border-radius: 3rem;
}

  
  .table {
    position: absolute;
    top: 1.5rem;
    left: 0.75rem;
    width:8.6rem;
  height: 5.5rem;
  font-size: 0.08rem;
    text-align: center;
  
    // color: #02a7f0;
  }
    
  .page {
    padding: 10px 0;
    position: absolute;
    top: 7.3rem;
  left: 3.9rem;
  }
    
    element.style {
      height: 0px;
    }
    .manage-header {
          display: flex;
          // justify-content: space-between;
          align-items: center;
      }
