                      <el-breadcrumb-item :to="{ path: '/home' }" style="font-size: 0.09rem;">首页</el-breadcrumb-item>
                      <el-breadcrumb-item :to="{ path: '/devicehome/DeviceInspectPlan' }" style="font-size: 0.09rem;">设备送检计划</el-breadcrumb-item>
    
                  </el-breadcrumb>
        <h1>设备送检计划</h1>
        <el-form class="manage-header">
        <el-form-item style="margin-left: 20%; width: 40vh;">
              <el-input placeholder="请输入仪器设备名称" v-model="planForm.name" ></el-input>
          </el-form-item>
          <el-form-item>
              <el-button @click="load2" type="primary" style="margin-left: 0.2rem; width:0.4rem; height:0.22rem;font-size:0.08rem;display: flex; align-items: center; justify-content: center;">查询</el-button> </el-form-item>
          <el-form-item>
              <el-button type="warning" @click="reset" style="margin-left: 0.2rem; width:0.4rem; height:0.22rem;font-size:0.08rem;display: flex; align-items: center; justify-content: center;">重置</el-button>
          </el-form-item>
        </el-form>
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
              <el-tab-pane label="计量工程中心送检计划" name="first"></el-tab-pane>
              <el-tab-pane label="EMC中心仪器送检计划" name="second"></el-tab-pane>
              <el-tab-pane label="安规中心仪器送检计划" name="third"></el-tab-pane>
              <el-tab-pane label="安规中心功能性检查送检计划" name="fourth"></el-tab-pane>
      </el-tabs>
        <!-- 主要表格 -->
        <el-table height="4.5rem" :data="tableData" style="width: 100%" border stripe
          @selection-change="handleSelectionChange">
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="num" label="自编号" align="center"></el-table-column>
            <el-table-column prop="name" label="仪器设备名称" align="center"></el-table-column>
            <el-table-column prop="model" label="型号" align="center"></el-table-column>
            <el-table-column prop="facNum" label="出厂号" align="center"></el-table-column>
            <el-table-column prop="scheTime" label="计划送检时间" align="center"></el-table-column>
            <el-table-column prop="cycle" label="周期（年）" align="center"></el-table-column>
            <el-table-column prop="verOrg" label="检定/校准机构" align="center"></el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
            <!-- <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
