  </template>
  
  <script>
  import HomeHeader from "../../../components/HomeHeader.vue";
  import HomeFooter from "../../../components/HomeFooter.vue";
  export default {
    components: { HomeHeader, HomeFooter },
    data() {
        return {
            dialogVisible: false,
            multipleSelection: [],
            activeName: 'first',
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            form: {
                id: '',
                num: '',
                name: '',
                model: '',
                facNum: '',
                scheTime: '',
                cycle: '',
                verOrg: '',
                remarks: ''
            },
            planForm: {
                id: '',
                num: '',
                name: '',
                model: '',
                fac_num: '',
                sche_time: '',
                cycle: '',
                ver_org: '',
                remarks: ''
            },
            rules: {
                num: [
                    { required: true, message: "请输入自编号", trigger: "blur" }
                ],
                name: [
                    { required: true, message: "请输入仪器设备名称", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "请输入型号", trigger: "blur" }
                ],
                fac_num: [
                    { required: true, message: "请输入出厂号", trigger: "blur" }
                ],
                sche_time: [
                    { required: true, message: "请选择计划送检时间", trigger: "blur" }
                ],
                cycle: [
                    { required: true, message: "请输入周期（年）", trigger: "blur" }
                ],
                ver_org: [
                    { required: true, message: "请输入检定/校准机构", trigger: "blur" }
                ]
            }
        }
    },
    created() {
        this.load()
    },
    methods: {
      handleClick() {
            // console.log(this.activeName);
            this.pageNum=1
            this.load()

        },
      indexMethod(index) {
      let curpage = this.pageNum   //单前页码，具体看组件取值
      let limitpage = this.pageSize   //每页条数，具体是组件取值
      return (index + 1) + (curpage - 1) * limitpage
     },
     reset() {
            this.verPlanForm.examineName = ""
            this.load()
        },
    load2(){
        this.pageNum = 1
        this.load()
    },
     load() {
            this.request.get("/equipInspPlan/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.planForm.name,
                    type:this.activeName,
                }
            })
                .then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        reset() {
        this.planForm.name = ""
        this.load()
        },
      // submit() {
      //   this.request.post("/order", this.form).then((res) => {
      //     //res为后台返回的结果
      //     if (res.code === "200") {
      //       this.$message.success("修改成功");
      //       this.dialogVisible = false;
      //       this.load();
      //     } else {
      //       this.$message.error("操作失败");
      //     }
      //   });
      // },
      cancel() {
        this.dialogVisible = false;
      },
      handleSelectionChange(val) {
        console.log(val);
        this.multipleSelection = val;
      },
      handleCurrentChange(pageNum) {
        this.pageNum = pageNum;
        this.load();
      },
      handleSizeChange(pageSize) {
        this.pageSize = pageSize;
        this.load();
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      handleEdit(row) {
        this.modalType = 1;
        // 注意需要对当前行数据进行深拷贝，否则会出错
        this.form = JSON.parse(JSON.stringify(row));
        this.dialogVisible = true;
      },
      handleDelete(id) {
        this.$confirm("是否确定取消预约", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        })
          .then(() => {
            this.request.delete("/order/" + id).then((res) => {
              if (res.code === "200") {
                this.$message({
                  type: "success",
                  message: "操作成功!",
                });
              }else{
                this.$message({
                  type: "error",
                  message: "距离就餐时间小于三十分钟，不可取消预约操作!",
                });
              }
  
              // 重新获取列表的接口
              this.load();
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消操作",
            });
          });
      },
      devicelist() {
        this.$router.push({ path: '/devicehome/devicelist' })
      },
      periodverificationplanf(){
        this.$router.push({ path: '/devicehome/periodverificationplanf' })
      },
      FixedAssets(){
        this.$router.push({ path: '/devicehome/FixedAssets' })
      }
    },
  };
  </script>
  
