<template>
    <div class="manage">
        <!-- 用户新增表单 -->
        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="工号" prop="id">
                    <el-input placeholder="请输入工号" v-model="form.id"></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="loginName">
                    <el-input placeholder="请输入姓名" v-model="form.loginName"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input placeholder="请输入密码" v-model="form.password"></el-input>
                </el-form-item>
                <!-- <el-form-item label="有效期" prop="endTime">
                    <el-date-picker v-model="form.endTime" type="date" placeholder="选择日期">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="角色" prop="role">
                    <el-select v-model="form.role" placeholder="请选择角色">
                        <el-option v-for="item in role" :label="item.name" @change="change" :value="item.name"
                            :key="item.name"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 用户编辑表单 -->
        <el-dialog title="提示" :visible.sync="updateDialogVisible" width="50%" :before-close="updateCancel">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="工号" prop="id">
                    <el-input placeholder="请输入工号" v-model="form.id" disabled></el-input>
                </el-form-item>
                <el-form-item label="姓名" prop="loginName">
                    <el-input placeholder="请输入姓名" v-model="form.loginName"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input placeholder="请输入密码" v-model="form.password"></el-input>
                </el-form-item>
                <!-- <el-form-item label="有效期" prop="endTime">
                    <el-date-picker v-model="form.endTime" type="date" placeholder="选择日期">
                    </el-date-picker>
                </el-form-item> -->
                <el-form-item label="角色" prop="role">
                    <el-select v-model="form.role" placeholder="请选择角色">
                        <el-option v-for="item in role" :label="item.name" @change="change" :value="item.name"
                            :key="item.name"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="updateCancel">取 消</el-button>
                <el-button type="primary" @click="submitUpdate">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 页头区域 -->
        <el-form :inline="true" :model="userForm">
            <!-- 新增用户 -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary">新增用户</el-button>
            </el-form-item>
            <!-- 批量删除 -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">批量删除</el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="exp" style="margin-left: 10px;" type="primary">导出详细信息<i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <!--form搜索区-->
            <el-form-item style="margin-left: 440px; ">
                <el-input placeholder="请输入工号" v-model="userForm.id"></el-input>
            </el-form-item>
            <!-- <el-form-item>
                <el-button @click="load" type="primary">查询</el-button>
            </el-form-item> -->
            <el-form-item>
                <el-input placeholder="请输入账户" v-model="userForm.loginName"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary">查询</el-button>
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 主要表格     -->
        <div class="common-table">
            <el-table :row-style="{ height: '55px' }" :data="tableData" style="width: 7.5rem;" border stripe
                @selection-change="handleSelectionChange">
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column type="index" label="序号" width="70" :index="indexMethod"></el-table-column>
                <el-table-column prop="id" label="工号" align="center">
                </el-table-column>
                <el-table-column prop="loginName" label="账户" align="center">
                </el-table-column>
                <!-- <el-table-column prop="password" label="密码" align="center">
                </el-table-column> -->
                <!-- <el-table-column prop="endTime" label="有效期" align="center"> -->
                <!-- </el-table-column> -->
                <el-table-column prop="role" label="角色" align="center">
                </el-table-column>
                <!-- <el-table-column prop="enable" label="启用" align="center" width="80">
                    <template slot-scope="scope">
                        <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                            @change="changeEnable(scope.row)"></el-switch>
                    </template>
                </el-table-column> -->
                <el-table-column label="操作" width="300" align="center">
                    <template slot-scope="scope">
                        <!-- <el-button size="mini" type="primary" :disabled="{user}.role !== '超级管理员'" @click="handleEdit(scope.row)">编辑</el-button> -->
                        <!-- <el-button size="mini" type="primary" :disabled="disableEditButton(scope.row)" @click="handleEdit(scope.row)">编辑</el-button> -->
                        <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>

                        <el-popconfirm class="ml-5" confirm-button-text="确定" cancel-button-text="我再想想" icon="el-icon-info"
                            icon-color="red" title="您确定删除吗？" @confirm="del(scope.row.id)">
                        </el-popconfirm>
                        <el-button style="margin-left: 5px" size="mini" type="info"
                            @click="handleView(scope.row.id)">查看</el-button>
                        <el-button style="margin-left:5px" size="mini" type="warning"
                            @click="resetPassword(scope.row.id)">重置密码</el-button>

                        <el-button style="margin-left:5px" size="mini" type="danger"
                            @click="handleDelete(scope.row.id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <user-details-card v-if="viewDialogVisible" :user="viewUser" @close="handleCloseDetails"></user-details-card>
        </div>

        <!-- 页码 -->
        <div class="pager">
            <el-pagination @size-change="handeleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
 
<script>
import UserDetailsCard from './UserDetailsCard.vue';//导入卡片弹窗组件

export default {
    components: {
        'user-details-card': UserDetailsCard,//注册卡片弹组件
    },
    data() {
        return {
            user: null,
            viewUser: null, //新属性，用于存储用户详细信息
            dialogVisible: false,
            updateDialogVisible: false,
            viewDialogVisible: false,
            multipleSelection: [],
            form: {
                id: "",
                loginName: "",
                password: "",
                // createTime: "",
                // endTime: "",
            },
            rules: {
                id: [
                    { required: true, message: "请输入工号", trigger: "blur" }
                ],
                loginName: [
                    { required: true, message: "请输入账户", trigger: "blur" }
                ],
                password: [
                    { required: true, message: "请输入密码", trigger: "blur" }
                ],
                endTime: [
                    { required: true, message: "请选择有效期", trigger: "blur" }
                ],
                role: [
                    { required: true, message: "请选择角色", trigger: "blur" }
                ]
            },
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            userForm: {
                id: '',
                loginName: '',
            },
            role: [],
        }
    },
    mounted() {
    },
    created() {
        this.user = localStorage.getItem("user") ? JSON.parse(localStorage.getItem("user")) : {}


        // console.log(this.user)
        //请求分页查询
        this.load()
        this.loadRole()
    },
    methods: {
        disableEditButton(row) {
            return this.user.role !== '超级管理员';
        },
        //查询重置
        reset() {

            this.userForm.loginName = ""
            this.userForm.id = ""
            this.load()
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        //提交用户表单
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/user", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("保存成功")
                            this.dialogVisible = false

                            this.load()
                        } else {

                            this.$message.error("新增失败，用户工号存在！")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        //编辑用户表单
        submitUpdate() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/user/update", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("修改成功")
                            this.updateDialogVisible = false
                            if (this.user.id === this.form.id) {

                                localStorage.setItem("user", JSON.stringify(this.form))
                            }
                            this.load()
                        } else {

                            this.$message.error("修改失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        //弹窗关闭时
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        updateCancel() {
            this.updateDialogVisible = false
        },
        handleEdit(row) {
            this.form.role
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            // this.dialogVisible = true
            this.updateDialogVisible = true

        },
        resetPassword(id) {
            this.$confirm('此操作将重置该用户密码, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/user/rePwd/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '重置成功!'
                        });

                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消重置'
                });
            });
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/user/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleView(job_num) {
            this.request.get("/userInfo/" + job_num)
                .then(response => {
                    const userDetails = response.data; // 从后端响应中获取用户详细信息
                    this.viewUser = userDetails;
                    this.viewDialogVisible = true;
                    // console.log("userDetails:", userDetails);
                }).catch(error => {
                    console.error("获取用户详细信息失败", error);
                    // 在请求失败时，你可以显示一个错误消息或采取其他适当的操作
                });
        },
        handleCloseDetails() {
            this.viewUser = null;//清空viewUser
            this.dialogVisible = false;//影藏模态框
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                // this.axios.post("http://172.25.183.18:9090/user/del/batch", ids).then(res => {
                this.request.post("/user/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        //导出用户详细信息表格
        exp() {

            // window.location.href="http://192.168.1.14:9090/userInfo/export"
            window.location.href = "http://192.168.1.14:9090/userInfo/export"


        },

        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/user/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    loginName: this.userForm.loginName,
                    id: this.userForm.id
                }
            }).then(res => {
                // this.tableData = res.data.records
                this.tableData = res.data.records.filter(item => item.role !== "系统管理员");
                this.total = res.data.total

            })


        },
        handeleSizeChange(pageSize) {
            console.log("pageSize:" + pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        // changeEnable(row) {
        //     console.log("1111")
        //     this.request.post("/user", row).then(res => {
        //         if (res.code === '200') {
        //             this.$message.success("操作成功")
        //         } else {
        //             this.$message.error("操作失败")
        //         }
        //     })
        // },
        loadRole() {
            this.request.get("/role")
                .then(res => {
                    console.log(res)
                    this.role = res.data
                    console.log(this.role)
                }
                )
        },
        change() {

        }
    },
}
</script>

