<template>
    <div class="manage">
        <!-- 用户新增表单 -->
        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="80px">
                <el-form-item label="设备名称" prop="examineName">
                    <el-input placeholder="请输入仪器名称" v-model="form.examineName"></el-input>
                </el-form-item>
                <el-form-item label="型号" prop="model">
                    <el-input placeholder="请输入型号" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="编号" prop="number">
                    <el-input placeholder="请输入编号" v-model="form.number"></el-input>
                </el-form-item>

                <!-- <el-form-item label="型号" prop="model">
                    <el-select placeholder="请选择型号" v-model="form.model" @change="updateNumberOptions">
                        <el-option v-for="(item, index) in modelOptions" :key="index" :label="item" :value="item"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="编号" prop="number">
                    <el-select placeholder="请选择编号" v-model="form.number" disabled>
                        <el-option v-for="(item, index) in numberOptions" :key="index" :label="item" :value="item"></el-option>
                    </el-select>
                </el-form-item> -->

                <el-form-item label="核查时间" prop="examineTime">
                    <el-date-picker v-model="form.examineTime" type="date" placeholder="选择日期" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <!-- <el-form-item  prop="type">
                    <el-input v-model="form.type" v-show="false"></el-input>
                    
                </el-form-item> -->

                <el-form-item label="添加到" prop="type">
                    <el-select placeholder="请选择地点" v-model="form.type">
                        <el-option v-for="item in placeOptions" :key="item.value" :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="备注" prop="remarks">
                    <el-input placeholder="请输入备注" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 页头区域 -->
        <el-form class="manage-header">
            <!-- 新增设备核查计划 -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary"
                    style="margin-left:1vh;width: 13vh;font-size: 1.5vh;padding-left: 12%;">新增核查计划</el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger"
                    style="margin-left: 5%;width: 13vh;font-size: 1.5vh;padding-left: 18%;">批量删除</el-button>
            </el-form-item>
            <el-form-item>
                <!-- <el-upload   action="'http://192.168.1.14:9090/examinePlan/import?type=' + 'this.activeName'"  :show-file-list="false" accept="xlsx"  -->
                <el-upload :action="exportUrl" :show-file-list="false" accept="xlsx" :on-success="handleExcelImportSuccess"
                    :on-error="handleExcelImportFail" style="display: inline-block;">
                    <el-button style="margin-left: 3vh;width: 9vh;font-size: 1.5vh;padding-left: 21%;" class="ml-5"
                        type="primary">导入<i class="el-icon-bottom"></i></el-button>
                </el-upload> </el-form-item>
            <el-form-item>
                <el-button @click="exp" style="margin-left: 3vh;width: 9vh;font-size: 1.5vh; padding-left: 21%;"
                    type="primary">导出<i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 2vh;" class="download-template-btn"
                    type="text">下载导入模板</el-button>
            </el-form-item>
            <el-form-item style="margin-left: 45%">
                <el-input placeholder="请输入设备名" v-model="verPlanForm.examineName"
                    style="width: 20vh;font-size: 1.5vh;margin-left:-55%"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary"
                    style="margin-left: -8vh;width:7.5vh ;font-size: 1.5vh;">查询</el-button> </el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset"
                    style="margin-left: 3vh;width:7.5vh ;font-size: 1.5vh;padding-left: 21%;">重置</el-button>
            </el-form-item>

        </el-form>
        <el-form>
            <el-form-item style="color: red; font-weight: bold; margin-left: 5vh; white-space: nowrap; width: 100%;">请注意，{{
                this.dayCount }} 台设备即将在30天内到期
            </el-form-item>
        </el-form>
        <!-- 选项卡 -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="计量工程中心核查计划" name="first"></el-tab-pane>
            <el-tab-pane label="EMC中心仪器核查计划" name="second"></el-tab-pane>
            <el-tab-pane label="安规中心仪器核查计划" name="third"></el-tab-pane>
            <el-tab-pane label="安规中心功能性检查核查计划" name="fourth"></el-tab-pane>
        </el-tabs>
        <!-- 主要表格 -->
        <el-table height="3rem" :data="tableData" style="width: 100%" border @selection-change="handleSelectionChange"
            :row-class-name="tableRowClassName">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="examineName" label="仪器设备名称" align="center"></el-table-column>
            <el-table-column prop="model" label="型号" align="center"></el-table-column>
            <el-table-column prop="number" label="编号" align="center"></el-table-column>
            <el-table-column prop="examineTime" label="核查时间" align="center"></el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"></el-table-column>
            <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-popconfirm class="ml-5" confirm-button-text="确定" cancel-button-text="我再想想" ican="el-icon-info"
                        icon-color="red" title="您确定删除吗？" @confirm="del(scope.row.id)">
                    </el-popconfirm>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import request from '@/utils/request';
export default {
    data() {
        return {
            dialogVisible: false,
            tableData: [],
            multipleSelection: [],
            activeName: 'first',

            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            dayCount: 0,
            form: {
                examineName: '',
                model: '',
                number: '',
                examineTime: '',
                type: '',
                remarks: ''
            },
            verPlanForm: {
                examineName: '',

            },
            rules: {
                examineName: [
                    { required: true, message: "请输入设备名称", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "请选择型号", trigger: "blur" }
                ],
                number: [
                    { required: true, message: "请输入编号", trigger: "blur" }
                ],
                examineTime: [
                    { required: true, message: "请选择核查时间", trigger: "blur" }
                ],
                type: [
                    { required: true, message: "请选择设备选项卡", trigger: "blur" }
                ]


            },

            //TODO：可能需要按顺序排序，加入更多型号
            modelOptions: ['ESCI', 'FSV 30', 'VULB9163', 'BBHA 9120D', 'FPA-6592G', '8449B',
                'SG6000', '150W1000', 'BLMA1060-200', 'STLP 9128D', 'FL7006', 'PM2002'],
            numberOptions: ['100983', '101374', '1178', '920', '060020', '3008A02604',
                '0327336', '0332731', '1711332', '17', '0332270', '0328313'],
            placeOptions: [{
                value: 'first',
                label: '计量工程中心核查计划'
            }, {
                value: 'second',
                label: 'EMC中心仪器核查计划'
            }, {
                value: 'third',
                label: '安规中心仪器核查计划'
            }, {
                value: 'fourth',
                label: '安规中心功能性检查核查计划'
            }]

            // rules:{

            // }

        }
    },
    created() {
        this.load()
        this.getDayCount()
    },
    computed: {
        exportUrl() {
            // 
            // console.log( 11)
            // console.log(  this.request.defaults.baseURL)
            // return  this.request.defaults.baseURL+"/examinePlan/import?type="+ this.activeName
            return "http://192.168.1.14:9090/examinePlan/import?type=" + this.activeName
        },
    },
    methods: {
        getDayCount() {
            this.request.get("/examinePlan/expire").then(res => {
                // console.log("hideField")
                // console.log(res)
                this.dayCount = res.data;
            });
        },

        // exportUrl: ,
        handleClick() {
            console.log(this.activeName);
            this.pageNum = 1
            this.load()

        },
        downloadTemplate() {
            // window.open("http://192.168.1.14:9090/file/48a507aab0ac4e11a604f1002034b3be.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/48a507aab0ac4e11a604f1002034b3be.xlsx"
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/examinePlan/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.verPlanForm.examineName,
                    type: this.activeName
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total
            }
            )
        },
        //查询重置
        reset() {
            this.verPlanForm.examineName = ""
            this.load()
        },
        //联动选择
        updateNumberOptions() {
            const model = this.form.model;
            const index = this.modelOptions.indexOf(model);
            if (index !== -1) {
                this.form.number = this.numberOptions[index];
            } else {
                this.form.number = '';
            }
        },
        //导出表格
        exp() {
            // window.open("http://192.168.1.14:9090/examinePlan/export")
            window.location.href = "http://192.168.1.14:9090/examinePlan/export/" + this.activeName
        },

        //导入成功加载钩子
        handleExcelImportSuccess() {
            this.$message.success("文件导入成功")
            this.load()
        },
        //导入失败加载钩子
        handleExcelImportFail() {
            this.$message.error("导入失败，请注意上传文件中的字段格式")
            this.load()
        },
        //增加
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        //弹窗关闭时
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //提交核查计划表单
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/examinePlan", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("保存成功")
                            this.dialogVisible = false
                            this.activeName = this.form.type
                            this.load()
                        } else {
                            this.$message.error("保存失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        handleEdit(row) {
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/examinePlan/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/examinePlan/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },

        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            // this.axios.post("http://192.168.1.14:9090/examinePlan", row).then(res => {
            this.request.post("/examinePlan", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
        tableRowClassName({ row, rowIndex }) {
            const today = new Date();
            const validityDate = new Date(row.examineTime);
            console.log("validityDate", validityDate);

            // 计算有效期与今天的天数差
            const daysRemaining = Math.floor((validityDate - today) / (1000 * 60 * 60 * 24) + 1);


            // 判断是否不到30天
            if (0 <= daysRemaining && daysRemaining <= 30) {

                // return this.selectedColor; // 返回用户选择的颜色作为类名
                return { 'highlight-row': true, 'top': '0' }; // 返回自定义类名
            }

            if (daysRemaining < 0) {
                return { 'gray-row': true, 'bottom': '0' }; // 返回自定义类名
            }

            return '';// 如果不应用额外类，可以考虑添加一个默认的排序权重
        },
    },
};
</script>

<style lang="less" scoped>
