    border: none;
    background: none;
    // color: blue;
    text-decoration: underline;
    cursor: pointer;
    margin-left: 1vh;
}

/deep/ .highlight-row {
    background-color: red;

    top: 0;
}

/deep/ .gray-row {
    background-color: lightgray;
    font-weight: bold;

    bottom: 0;
}

.manage {
    height: 2rem;
    width: 95%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>