<template>
    <div class="manage">
        <!-- 页头区域 -->
        <el-form :inline="true" :model="userForm" class="manage-header">
            <!-- 新增角色 -->
            <!-- <el-form-item>
                <el-upload action="http://localhost:9090/order/upload" :show-order-list="false"
                    :on-success="handleFileUploadSuccess" style="display: inline-block">
                    <el-button type="primary" class="ml-5">新增角色</el-button>
                </el-upload>
            </el-form-item> -->
            <!-- 批量删除 -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">
                    批量删除
                </el-button>
            </el-form-item>
            <!--form搜索区-->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="请输入用户名全称" v-model="userForm.orderUser"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary">查询</el-button>
                <el-button type="warning" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 主要表格 -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="mealDate" label="就餐日期" align="center"></el-table-column>
            <el-table-column prop="mealTime" label="就餐时间" align="center"></el-table-column>
            <el-table-column prop="orderUser" label="订餐用户" align="center"></el-table-column>
            <el-table-column prop="tel" label="电话" align="center"></el-table-column>
            <el-table-column prop="orderNum" label="订餐数" align="center"></el-table-column>
            <!-- <el-table-column prop="enable" label="启用" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column> -->
            <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],

            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            userForm: {
                orderUser: '',
            }
        }
    },
    created() {
        this.load()
    },
    methods: {
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/order/back/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/order/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {

            this.request.get("/order/pageRecord" , {
                params: {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                orderUser: this.userForm.orderUser
                }
            }).then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        reset() {
            this.userForm.orderUser = ""
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            console.log("1111")
            this.request.post("/order", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>