<template>
    <div class="manage">
        <!-- 新增计划表单 -->
        <el-dialog title="新增设备送检计划表单" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="140px" >
                <el-form-item label="自编号" prop="num">
                    <el-input placeholder="请输入自编号" v-model="form.num"></el-input>
                </el-form-item>
                <el-form-item label="仪器设备名称" prop="name" >
                    <el-input placeholder="请输入仪器设备名称" v-model="form.name" ></el-input>
                </el-form-item>
                <el-form-item label="型号" prop="model">
                    <el-input placeholder="请输入型号" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="出厂号" prop="facNum">
                    <el-input placeholder="请输入出厂号" v-model="form.facNum"></el-input>
                </el-form-item>
                <el-form-item label="计划送检时间" prop="scheTime">
                    <el-date-picker v-model="form.scheTime" type="date" placeholder="选择计划送检时间" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="周期（年）" prop="cycle">
                    <el-input placeholder="请输入周期（年）" v-model="form.cycle"></el-input>
                </el-form-item>
                <el-form-item label="检定/校准机构" prop="verOrg">
                    <el-input placeholder="请输入检定/校准机构" v-model="form.verOrg"></el-input>
                </el-form-item>
                <el-form-item label="添加到" prop="type">
                    <el-select placeholder="请选择地点" v-model="form.type" >
                        <el-option v-for="item in placeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="备注" prop="remarks">
                    <el-input placeholder="请输入备注" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 页头区域 -->
        <el-form class="manage-header">
            <el-form-item style="width: 50vh;">
                <el-button @click="handleAdd" type="primary" style="margin-left:1vh;width: 13vh;font-size: 1.5vh;padding-left: 12%;">新增送检计划</el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 5%;width: 13vh;font-size: 1.5vh;padding-left: 18%;">批量删除</el-button>
            </el-form-item>
            <el-form-item>
                <!-- <el-upload   action="http://192.168.1.14:9090/equipInspPlan/import" :show-file-list="false" accept="xlsx"  -->
                <el-upload   :action="exportUrl"  :show-file-list="false" accept="xlsx" 
                      
                :on-success="handleExcelImportSuccess"
                :on-error="handleExcelImportFail"
                style="display: inline-block;">
                <el-button  style="margin-left: 3vh;width: 9vh;font-size: 1.5vh;padding-left: 21%;"  class = "ml-5" type="primary">导入<i class="el-icon-bottom"></i></el-button>
                </el-upload> </el-form-item>
            <el-form-item>
                <el-button @click="exp"  style="margin-left: 3vh;width: 9vh;font-size: 1.5vh;padding-left: 21%;" type="primary">导出<i class="el-icon-top">
                </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 2vh;" class="download-template-btn" type="text">下载导入模板</el-button>
            </el-form-item>
            <el-form-item style="margin-left:45% ;">
                <el-input placeholder="请输入仪器设备名称" v-model="planForm.name" style="width: 20vh;font-size: 1.5vh; margin-left:-55%"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary" style="margin-left: -8vh;width:7.5vh ;font-size: 1.5vh;">查询</el-button>
             </el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset" style="margin-left:3vh;width: 7.5vh;font-size: 1.5vh;padding-left: 21%;">重置</el-button>
            </el-form-item>
        </el-form>
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
                <el-tab-pane label="计量工程中心送检计划" name="first"></el-tab-pane>
                <el-tab-pane label="EMC中心仪器送检计划" name="second"></el-tab-pane>
                <el-tab-pane label="安规中心仪器送检计划" name="third"></el-tab-pane>
                <el-tab-pane label="安规中心功能性检查送检计划" name="fourth"></el-tab-pane>
        </el-tabs>
        <!-- 主要表格 -->
        <el-table  :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="num" label="自编号" align="center"></el-table-column>
            <el-table-column prop="name" label="仪器设备名称" align="center"></el-table-column>
            <el-table-column prop="model" label="型号" align="center"></el-table-column>
            <el-table-column prop="facNum" label="出厂号" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="scheTime" label="计划送检时间" align="center"></el-table-column>
            <el-table-column prop="cycle" label="周期（年）" align="center"></el-table-column>
            <el-table-column prop="verOrg" label="检定/校准机构" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column prop="remarks" label="备注" align="center"  show-overflow-tooltip></el-table-column>
            <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogVisible: false,
            multipleSelection: [],
            activeName: 'first',
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            form: {
                id: '',
                num: '',
                name: '',
                model: '',
                facNum: '',
                scheTime: '',
                cycle: '',
                verOrg: '',
                remarks: ''
            },
            planForm: {
                id: '',
                num: '',
                name: '',
                model: '',
                fac_num: '',
                sche_time: '',
                cycle: '',
                ver_org: '',
                remarks: ''
            },
            rules: {
                num: [
                    { required: true, message: "请输入自编号", trigger: "blur" }
                ],
                name: [
                    { required: true, message: "请输入仪器设备名称", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "请输入型号", trigger: "blur" }
                ],
                facNum: [
                    { required: true, message: "请输入出厂号", trigger: "blur" }
                ],
                scheTime: [
                    { required: true, message: "请选择计划送检时间", trigger: "blur" }
                ],
                cycle: [
                    { required: true, message: "请输入周期（年）", trigger: "blur" }
                ],
                ver_org: [
                    { required: true, message: "请输入检定/校准机构", trigger: "blur" }
                ],
                 type: [
                    { required: true, message: "请选择设备选项卡", trigger: "blur" }
                ]


            },
            placeOptions: [{
                value: 'first',
                label: '计量工程中心送检计划'
                }, {
                value: 'second',
                label: 'EMC中心仪器送检计划'
                }, {
                value: 'third',
                label: '安规中心仪器送检计划'
                },  {
                value: 'fourth',
                label: '安规中心功能性检查送检计划'
                }]

        }
    },
    created() {
        this.load()
    },
    computed:{
        exportUrl(){
            return "http://192.168.1.14:9090/equipInspPlan/import?type=" + this.activeName
        },
    },
    methods: {
        handleClick() {
            console.log(this.activeName);
            this.pageNum=1
            this.load()

        },
        downloadTemplate(){
            // window.open("http://192.168.1.14:9090/file/d8ee16247c6a4d51b42a44ab5631db2e.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/d8ee16247c6a4d51b42a44ab5631db2e.xlsx"

        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        //提交表单
        submit() {
            this.$refs.form.validate((valid)=>{
            if(valid){
            this.request.post("/equipInspPlan", this.form).then(res => {
                if (res.code === "200") {
                    this.$message.success("保存成功")
                    this.dialogVisible = false
                    this.activeName = this.form.type
                    this.load()
                } else {
                    this.$message.error("保存失败")
                }
            })
            }else{
                    return false;                    
                }
             })
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //查询重置
        reset() {
        this.planForm.name = ""
        this.load()
        },
        //导出表格
        exp(){
            // window.open("http://192.168.1.14:9090/equipInspPlan/export")
            window.location.href="http://192.168.1.14:9090/equipInspPlan/export/"+ this.activeName

         },
         
         //导入成功加载钩子
         handleExcelImportSuccess(){ 
            this.$message.success("文件导入成功")
            this.load()
         },
         //导入失败加载钩子
        handleExcelImportFail(){
            this.$message.error("导入失败，请注意上传文件中的字段格式")
            this.load()
         },
        handleEdit(row) {
            this.modalType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该计划, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipInspPlan/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipInspPlan/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/equipInspPlan/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.planForm.name,
                    type:this.activeName,
                }
            })
                .then(
                    res => {
                        console.log(res)
                        this.tableData = res.data.records
                        this.total = res.data.total
                    }
                )
        },
        handleSizeChange(pageSize) {
            console.log("pageSize:" + pageSize)
            this.pageSize = pageSize
            this.load()
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        changeEnable(row) {
          
            this.request.post("/equipInspPlan", row).then(res => {
                if (res.data.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.download-template-btn {
    border: none;
    background: none;
    // color: blue;
    text-decoration: underline;
    cursor: pointer;
    margin-left: 1vh;
  }
.manage {
    height: 90%;
    width: 95%;
    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>