      </div>
      <div class="box-card3">
        <h2 @click="deviceinspplan">设备送检计划</h2>
      </div>
      <div class="box-card4">
        <h2 @click="periodverificationplanf">期间核查计划</h2>
      </div>
      <div class="box-card10">
        <h2 @click="FixedAssets">固定资产</h2>
      </div>
    </div>
    <!-- 卡片区域 -->
    <div style="width:25rem;">
      <el-card class="table" :header-cell-style="{ color: 'green' }">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item :to="{ path: '/home' }" style="font-size: 0.09rem;">首页</el-breadcrumb-item>
