          </el-form-item>
          <el-form-item>
            <el-button @click="load2" type="primary"
              style="margin-left: 0.2rem; width:0.4rem; height:0.22rem;font-size:0.08rem;display: flex; align-items: center; justify-content: center;">查询</el-button></el-form-item>
          <el-form-item>
            <el-button type="warning" @click="reset"
              style="margin-left: 0.2rem; width: 0.4rem; height: 0.22rem; font-size: 0.08rem; padding: auto; display: flex; align-items: center; justify-content: center;">
              重置</el-button>

          </el-form-item>

        </el-form>
        <!-- 选项卡 -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="计量工程中心仪器设备" name="first"></el-tab-pane>
          <el-tab-pane label="EMC中心仪器设备" name="second"></el-tab-pane>
          <el-tab-pane label="安规中心仪器设备" name="third"></el-tab-pane>
          <el-tab-pane label="安规中心功能性检查设备" name="fourth"></el-tab-pane>
        </el-tabs>
        <!-- 主要表格 -->
        <el-table :data="tableData" style="width: 100%; font-size: 0.08rem;" border width="3rem"
          @selection-change="handleSelectionChange" :row-class-name="tableRowClassName">

          <el-table-column type="index" label="序号" width="60rem" align="center" :index="indexMethod"></el-table-column>
          <template v-for="item in columns">
            <el-table-column v-if="showColumn(item.prop)" :prop="item.prop" :key="item.label" :label="item.label"
              align="center"></el-table-column>
          </template>


        </el-table>
        <div class="page">
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
            :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="total">
          </el-pagination>
        </div>
