<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
export default {
  components: { HomeHeader, HomeFooter },
  data() {
    return {
      dialogVisible: false,
      multipleSelection: [],
      activeName: 'first',
      tableData: [],
      validityFilters: [],
      verOrgFilters: [],
      cycleFilters: [],
      filtersForm: {
        validity: [],
        verOrg: [],
        cycle: []
      },
      filteredData: [], // 筛选后的数据
      modelType: 0,//0表示新增的弹窗，1表示编辑,
      total: 0,//当前的总条数
      pageNum: 1,
      pageSize: 10,
      equipForm: {
        id: '',
        selfNum: '',
        equipName: '',
        model: '',
        facNum: '',
        cerNum: '',
        validity: '',
        cycle: '',
        verOrg: '',
        remarks: '',
        measureRange: '',
        accuracy: '',
        usePlace: '',
        manuFact: ''
      },
      form: {
        id: '',
        selfNum: '',
        equipName: '',
        model: '',
        facNum: '',
        cerNum: '',
        validity: '',
        cycle: '',
        verOrg: '',
        remarks: '',
        measureRange: '',
        accuracy: '',
        usePlace: '',
        manuFact: ''
      },
      columns: [
        //  {label:"自编号", prop:"selfNum" ,isShow:true},
        //  {label:"仪器设备名称",prop:"equipName",isShow:true},
        //  {label:"型号",prop:"model",isShow:true},


      ],
      rules: {
        selfNum: [
          { required: true, message: "请输入自编号", trigger: "blur" }
        ],
        equipName: [
          { required: true, message: "请输入仪器设备名称", trigger: "blur" }
        ],
        model: [
          { required: true, message: "请输入型号", trigger: "blur" }
        ],
        facNum: [
          { required: true, message: "请输入出厂号", trigger: "blur" }
        ],
        cerNum: [
          { required: true, message: "请输入证书编号", trigger: "blur" }
        ],
        validity: [
          { required: true, message: "请选择有效期", trigger: "blur" }
        ],
        cycle: [
          { required: true, message: "请输入周期", trigger: "blur" }
        ],
        verOrg: [
          { required: true, message: "请输入检定/校准机构", trigger: "blur" }
        ],
        measureRange: [
          { required: true, message: "请输入主要测量范围", trigger: "blur" }
        ],
        accuracy: [
          { required: true, message: "请输入准确度等级或最大允许误差或不确定度", trigger: "blur" }
        ],
        usePlace: [
          { required: true, message: "请输入使用地点", trigger: "blur" }
        ],
        manuFact: [
          { required: true, message: "请输入生产厂家", trigger: "blur" }
        ]
      }
    }
  },
  created() {
    this.load()
    this.getAll()
    this.getAllField()
  },
  methods: {
    //获取所有字段
    getAllField() {
      this.request.get("/hideField").then(res => {

        this.columns = res.data;
      });
    },
    // 表格列是否显示的方法
    showColumn(currentColumn) {
      return this.columns.find(item => item.prop == currentColumn).isShow;
    },
    handleClick() {
      // console.log(this.activeName);
      this.pageNum = 1
      this.load()

    },
    load2() {
      this.pageNum = 1
      this.load()
    },
    load() {
      this.request.get("/equipLedger/front/page", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          name: this.equipForm.name,
          model: this.equipForm.model,
          type: this.activeName,

        }
      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
        //重新加载数据时候需要清除下筛选变量
        this.getAll()

      }
      )
    },
    getAll() {
      this.request.get("/equipLedger").then(res => {
        const records = res.data;
        this.handleFilterField(records)

      });
    },
    reset() {
      this.equipForm.name = ""
      this.equipForm.model = ""
      this.activeName = "first"
      this.filtersForm.validity = ""
      this.filtersForm.verOrg = ""
      this.filtersForm.cycle = ""
      // this.$refs.filterTable.clearFilter()
      this.load()
    },
    handleFilterField(records) {
      // 提取日期数据
      // 第一版：不能单独筛选
      const dates = records.map(record => record.validity);

      // 使用 Set 对象来获取唯一的日期数据
      const uniqueDates = [...new Set(dates)];
      // 排序日期数据
      uniqueDates.sort((a, b) => new Date(a) - new Date(b));
      // 更新 this.tableData 和 this.validityFilters
      // this.tableData = records;
      this.validityFilters = uniqueDates.map(date => {
        return {
          text: date,
          value: date
        };
      });



      //检定/校准机构
      const verOrgs = records.map(record => record.verOrg)
      const uniqueVerOrgs = [...new Set(verOrgs)];
      this.verOrgFilters = uniqueVerOrgs.map(verOrg => {
        return {
          text: verOrg,
          value: verOrg
        };
      });
      //周期
      const cycles = records.map(record => record.cycle)
      const uniqueCycles = [...new Set(cycles)];
      this.cycleFilters = uniqueCycles.map(cycle => {
        return {
          text: cycle,
          value: cycle
        };
      });
    },
    handeleFilterChange(filter) {
      //在第二页筛选时，重新跳转到第一页
      this.pageNum = 1
      //联动筛选
      if (filter.validity)
        this.filtersForm.validity = filter.validity.join(',')
      if (filter.cycle)
        this.filtersForm.cycle = filter.cycle.join(',')
      if (filter.verOrg)
        this.filtersForm.verOrg = filter.verOrg.join(',')

      this.request.get("/equipLedger/filter", {
        params: {
          pageNum: this.pageNum,
          pageSize: this.pageSize,
          validity: this.filtersForm.validity,
          verOrg: this.filtersForm.verOrg,
          cycle: this.filtersForm.cycle
        }

      }).then(res => {
        this.tableData = res.data.records
        this.total = res.data.total
        this.handleFilterField(res.data.records)
      });
    },
    cancel() {
      this.dialogVisible = false;
    },
    handleSelectionChange(val) {
      console.log(val);
      this.multipleSelection = val;
    },
    handleCurrentChange(pageNum) {
      this.pageNum = pageNum;
      this.load();
    },
    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.load();
    },
    handleDelete(id) {
      this.$confirm("是否确定取消预约", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.request.delete("/order/" + id).then((res) => {
            if (res.code === "200") {
              this.$message({
                type: "success",
                message: "操作成功!",
              });
            } else {
              this.$message({
                type: "error",
                message: "距离就餐时间小于三十分钟，不可取消预约操作!",
              });
            }

            // 重新获取列表的接口
            this.load();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消操作",
          });
        });
    },
    deviceinspplan() {
      this.$router.push({ path: '/devicehome/deviceinspplan' })
    },
    FixedAssets() {
      this.$router.push({ path: '/devicehome/FixedAssets' })
    },
    periodverificationplanf() {
      this.$router.push({ path: '/devicehome/periodverificationplanf' })
    },

    tableRowClassName({ row, rowIndex }) {
      if (rowIndex === 1) {
        return 'warning-row';
      } else if (rowIndex === 3) {
        return 'success-row';
      }
      return '';
    },
    indexMethod(index) {
      let curpage = this.pageNum   //单前页码，具体看组件取值
      let limitpage = this.pageSize   //每页条数，具体是组件取值
      return (index + 1) + (curpage - 1) * limitpage
    },
    tableRowClassName({ row, rowIndex }) {
      const today = new Date();
      const validityDate = new Date(row.validity);
      // console.log("today" + today)
      // console.log("validityDate" + validityDate)
      validityDate.setMonth(validityDate.getMonth() + row.cycle);
      // 计算有效期与今天的天数差
      const daysRemaining = Math.floor((validityDate - today) / (1000 * 60 * 60 * 24) + 1);
      console.log("daysRemaining" + daysRemaining)

      // 判断是否不到30天
      if (0 <= daysRemaining && daysRemaining <= 30) {
        console.log(11111111111)
        return { 'highlight-row': true, 'top': '0' }; // 返回自定义类名
      }

      if (daysRemaining < 0) {
        console.log(11111111111)
        return { 'gray-row': true, 'bottom': '0' }; // 返回自定义类名
      }

      return '';// 如果不应用额外类，可以考虑添加一个默认的排序权重
    }
  },
};
</script>

