<template>
    <div class="manage">
        <!-- 设备新增表单 -->
        <el-dialog title="设备新增表单" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">

            <!-- 设备的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form" label-width="120px">
                <el-form-item label="自编号" prop="selfNum">
                    <el-input placeholder="请输入自编号" v-model="form.selfNum"></el-input>
                </el-form-item>
                <el-form-item label="设备状态" prop="equipStatus">
                    <el-select v-model="form.equipStatus" placeholder="请选择设备状态">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="设备名称" prop="equipName">
                    <el-input placeholder="请输入设备名称" v-model="form.equipName"></el-input>
                </el-form-item>
                <el-form-item label="型号" prop="model">
                    <el-input placeholder="请输入型号" v-model="form.model"></el-input>
                </el-form-item>
                <el-form-item label="出厂编号">
                    <el-input placeholder="请输入出厂编号" v-model="form.facNum"></el-input>
                </el-form-item>
                <el-form-item label="证书编号" prop="cerNum">
                    <el-input placeholder="请输入证书编号" v-model="form.cerNum"></el-input>
                </el-form-item>
                <el-form-item label="校准日期" prop="validity">
                    <el-date-picker v-model="form.validity" type="date" placeholder="请选择校准日期" value-format="yyyy-MM-dd">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="周期（月）" prop="cycle">
                    <el-input placeholder="请输入周期" v-model="form.cycle"></el-input>
                </el-form-item>
                <el-form-item label="检定/校准机构">
                    <el-input placeholder="请输入检定/校准机构" v-model="form.verOrg"></el-input>
                </el-form-item>

                <el-form-item label="主要测量范围">
                    <el-input placeholder="请输入主要测量范围" v-model="form.measureRange"></el-input>
                </el-form-item>

                <el-form-item label="准确度等级">
                    <el-input placeholder="准确度等级或最大允许误差或不确定度" v-model="form.accuracy"></el-input>
                </el-form-item>

                <el-form-item label="使用地点">
                    <el-input placeholder="请输入使用地点" v-model="form.usePlace"></el-input>
                </el-form-item>
                <el-form-item label="制造商" prop="manuFact">
                    <el-input placeholder="请输入制造商" v-model="form.manuFact"></el-input>
                </el-form-item>
                <el-form-item label="添加到" prop="type">
                    <el-select placeholder="请选择设备选项卡" v-model="form.type">
                        <el-option v-for="item in placeOptions" :key="item.value" :label="item.label"
                            :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="设备文档">
                    <el-input placeholder="请上传设备档案" v-model="form.purchCont"> </el-input>
                    <el-upload action="http://192.168.1.14:9090/fileEquipment/upload" :show-file-list="false"
                        :on-success="handleFileUploadSuccess" style="display: inline-block">
                        <el-button type="primary" size="mini" class="ml-5">上传文件</el-button>
                    </el-upload>
                </el-form-item>
                <el-form-item label="备注">
                    <el-input placeholder="请输入备注" v-model="form.remarks"></el-input>
                </el-form-item>
            </el-form>
            <!-- 按钮区域 -->
            <span slot="footer" class="dialog-footer">
                <el-button type="success" @click="open">添加到固定资产</el-button>
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>
        <!-- 页头区域 -->
        <el-form class="manage-header">
            <!-- 新增用户 -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary">新增设备台账</el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="handleBatchDelete" type="danger" style="margin-left: 3vh;">批量删除</el-button>
            </el-form-item>
            <el-form-item>
                <!-- <el-upload   action="http://192.168.1.14:9090/equipLedger/import" :show-file-list="false" accept="xlsx"  -->
                <el-upload :action="exportUrl" :show-file-list="false" accept="xlsx" :on-success="handleExcelImportSuccess"
                    :on-error="handleExcelImportFail" style="display: inline-block;">
                    <el-button style="margin-left: 30px;" class="ml-5" type="primary">导入<i
                            class="el-icon-bottom"></i></el-button>
                </el-upload></el-form-item> <el-form-item>
                <el-button @click="exp" style="margin-left: 30px;" type="primary">导出<i class="el-icon-top">
                    </i></el-button>
            </el-form-item>
            <el-form-item>
                <el-button @click="downloadTemplate" style="margin-left: 20px;" class="download-template-btn"
                    type="text">下载导入模板</el-button>
            </el-form-item>
            <el-form-item style="margin-left:20vh;">
                <el-input placeholder="请输入设备名称" v-model="equipForm.name" style="width: 20vh;"></el-input>
            </el-form-item>

            <el-form-item>
                <el-input placeholder="请输入设备型号" v-model="equipForm.model"
                    style="width:  20vh; margin-left: 20px;"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary" style="margin-left: 20px;">查询</el-button></el-form-item>
            <el-form-item>
                <el-button type="warning" @click="reset" style="margin-left: 10px;">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 筛选 -->
        <el-form inline style="width: 100%;">
            <el-form-item style="color: red; margin-top: 10px;  font-weight: bold; margin-left: 50px;">
                请注意, {{ this.dayCount }} 台设备即将在30天内到期
            </el-form-item>
            <el-form-item style="margin-left:27vh;">
                <el-input placeholder="请输入校准日期" v-model="equipForm.validity" style="width: 20vh;"></el-input>
            </el-form-item>

            <el-form-item style="margin-left: 20px;">
                <el-input placeholder="请输入周期(月)" v-model="equipForm.cycle" style="width: 20vh;"></el-input>
            </el-form-item>
            <el-form-item style="margin-left: 20px;">
                <el-input placeholder="请输入检定/校准机构" v-model="equipForm.verOrg" style="width: 20vh;"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary" style="margin-left:15px;">筛选</el-button>
                <el-button type="warning" @click="reset" style="margin-left: 18px;">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 自定义颜色 -->
        <!-- <el-color-picker v-model="selectedColor"></el-color-picker> -->
        <!-- 隐藏字段 -->
        <!-- <el-popover placement="left" trigger="hover" class="popover" :popper-options="popperOptions" @show="showPopover" > -->
        <el-popover placement="left" trigger="hover" class="popover" @show="showPopover">
            <div style="display: flex; justify-content: space-between;align-items: center; ">
                <el-checkbox v-model="checkAll" @change="checkAllChangeFn">列展示</el-checkbox>
                <el-button type="text" @click="resetEnable(true)">重置</el-button>
            </div>
            <el-divider></el-divider>
            <el-checkbox-group v-model="checkColumns" @change="changeColumns">
                <el-checkbox v-for="(item, key) in columns" :key="key" :label="item.label"></el-checkbox>
            </el-checkbox-group>
            <i class="el-icon-s-tools" slot="reference" style="margin-right: 1px;width: 5px;"></i>
        </el-popover>

        <!-- 选项卡 -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick" style="margin-top: -30px; width: 90%;">
            <el-tab-pane label="计量工程中心仪器设备" name="first"></el-tab-pane>
            <el-tab-pane label="EMC中心仪器设备" name="second"></el-tab-pane>
            <el-tab-pane label="安规中心仪器设备" name="third"></el-tab-pane>
            <el-tab-pane label="安规中心功能性检查设备" name="fourth"></el-tab-pane>
        </el-tabs>

        <!-- 主要表格 -->
        <el-table  :data="tableData" ref="filterTable" border @selection-change="handleSelectionChange"
            :row-class-name="tableRowClassName" @filter-change="handeleFilterChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <template v-for="item in columns">
                <el-table-column v-if="showColumn(item.prop)" :prop="item.prop" :key="item.label" :label="item.label"
                    align="center">
                </el-table-column>
            </template>
            <el-table-column prop="purchCont" label="设备文档" align="center" width="160px" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-link type="primary" @click="handleDownload(scope.row.purchContPath)">
                        {{ scope.row.purchCont }}
                    </el-link>
                </template>
            </el-table-column>
            <el-table-column prop="enable" label="显示" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enabled" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="200" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="primary" @click="handleEdit(scope.row)">编辑</el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            dialogVisible: false,
            multipleSelection: [],
            activeName: 'first',
            tableData: [],
            validityFilters: [],
            verOrgFilters: [],
            cycleFilters: [],
            filtersForm: {
                validity: [],
                verOrg: [],
                cycle: []
            },
            options: [{
                value: '合格',
                label: '合格'
            }, {
                value: '准用',
                label: '准用'
            }, {
                value: '停用',
                label: '停用'
            }],
            placeOptions: [{
                value: 'first',
                label: '计量工程中心仪器设备'
            }, {
                value: 'second',
                label: 'EMC中心仪器设备'
            }, {
                value: 'third',
                label: '安规中心仪器设备'
            }, {
                value: 'fourth',
                label: '安规中心功能性检查设备'
            }],

            filteredData: [], // 筛选后的数据
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            equipForm: {
                id: '',
                selfNum: '',
                equipName: '',
                model: '',
                facNum: '',
                cerNum: '',
                validity: '',
                cycle: '',
                verOrg: '',
                remarks: '',
                measureRange: '',
                accuracy: '',
                usePlace: '',
                manuFact: ''
            },
            form: {
                id: '',
                selfNum: '',
                equipName: '',
                model: '',
                facNum: '',
                cerNum: '',
                validity: '',
                cycle: '',
                verOrg: '',
                remarks: '',
                measureRange: '',
                accuracy: '',
                usePlace: '',
                manuFact: '',
                equipStatus: '',
                purchCont: '',
                purchContPath: '',
                fixedNum: '',
            },
            // selectedColor: '', // 保存用户选择的颜色
            dayCount: 0,
            checkAll: false,
            checkColumns: [],
            columns: [],
            rules: {
                selfNum: [
                    { required: true, message: "请输入自编号", trigger: "blur" }
                ],
                equipName: [
                    { required: true, message: "请输入仪器设备名称", trigger: "blur" }
                ],
                model: [
                    { required: true, message: "请输入型号", trigger: "blur" }
                ],
                facNum: [
                    { required: true, message: "请输入出厂号", trigger: "blur" }
                ],
                cerNum: [
                    { required: true, message: "请输入证书编号", trigger: "blur" }
                ],
                validity: [
                    { required: true, message: "请选择校准日期", trigger: "blur" }
                ],
                cycle: [
                    { required: true, message: "请输入周期", trigger: "blur" }
                ],
                verOrg: [
                    { required: true, message: "请输入检定/校准机构", trigger: "blur" }
                ],
                measureRange: [
                    { required: true, message: "请输入主要测量范围", trigger: "blur" }
                ],
                accuracy: [
                    { required: true, message: "请输入准确度等级或最大允许误差或不确定度", trigger: "blur" }
                ],
                usePlace: [
                    { required: true, message: "请输入使用地点", trigger: "blur" }
                ],
                manuFact: [
                    { required: true, message: "请输入生产厂家", trigger: "blur" }
                ],
                type: [
                    { required: true, message: "请选择设备选项卡", trigger: "blur" }
                ],
                equipStatus: [
                    { required: true, message: "请输入设备状态", trigger: "blur" }
                ],

            }
        }
    },
    created() {
        this.load()
        this.getAll()
        this.getAllField()
        this.getDayCount()
    },
    computed: {
        exportUrl() {
            return "http://192.168.1.14:9090/equipLedger/import?type=" + this.activeName
        },
    },
    methods: {

        handleDownload(path) {
            window.location.href = path
        },
        getDayCount() {
            this.request.get("/equipLedger/expire").then(res => {
                // console.log("hideField")
                // console.log(res)
                this.dayCount = res.data;
            });
        },
        handleFileUploadSuccess(res) {
            console.log(res)
            // this.form.purchCont=''
            if (res.code === "200") {
                this.form.purchCont = res.data.name
                this.form.purchContPath = res.data.path
                this.$forceUpdate();
                this.$message.success("上传成功")
                // console.log(  res.data.name)

                // this.refreshForm(); 
                // console.log(   this.form.purchCont)
            } else {
                this.$message.error("上传失败")
            }

            // this.load()
        },
        //隐藏列
        changeEnable(row) {

            this.request.post("/equipLedger", row).then(res => {
                // console.log("....")
                //  console.log(row)
                if (res.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
        //获取所有字段
        getAllField() {
            this.request.get("/hideField").then(res => {
                console.log("hideField")
                console.log(res)
                this.columns = res.data;
            });
        },
        updateHideField() {
            this.request.post("/hideField/batchSave", this.columns).then(res => {
                if (res.code === "200") {
                    this.$message.success("隐藏修改成功")
                    // this.dialogVisible = false
                    // this.activeName = this.form.type
                    this.load()
                } else {
                    this.$message.error("隐藏修改失败")
                }
            })

        },
        // 全选复选框事件监听
        checkAllChangeFn(val) {
            if (val) {
                // 全选
                this.resetEnable(true);
            } else {
                // 反全选
                this.resetEnable(false);
            }
        },
        // 表格列是否显示的方法
        showColumn(currentColumn) {
            return this.columns.find(item => item.prop == currentColumn).isShow;
        },

        // showColumn(currentColumn) {
        //     return this.columns.filter(item => item.prop === "purchCont").find(item => item.prop === currentColumn).isShow;
        // },
        //重置，flag: Boolean，全部重置为flag
        resetEnable(flag) {
            this.columns.forEach(item => {
                item.isShow = flag;
            })
            this.showPopover();
            this.refreshTable();
            //更新到数据库
            this.updateHideField();
        },

        /* 选择列 */
        changeColumns(val) {
            this.columns.forEach(item => {
                item.isShow = false;
            })
            // columns将val数组存在的值设为true，不存在的设为false
            val?.forEach(item => {
                let current = this.columns.find(i => i.label == item)
                current.isShow = true;
            })
            // 判断是否全选
            this.judgeIsCheckAll();
            this.refreshTable();
            //存入到数据库
            this.updateHideField();
        },
        // 重新渲染表格
        refreshTable() {
            this.$nextTick(() => {
                this.$refs.filterTable.doLayout();
            })
        },
        // 气泡框出现
        showPopover() {
            this.checkColumns = []
            this.columns.forEach(item => {
                if (item.isShow) {
                    this.checkColumns.push(item.label)
                }
            })
            // 判断是否全选
            this.judgeIsCheckAll();
        },
        // 判断是否全选
        judgeIsCheckAll() {
            // 选中的长度 = 表格列的长度  全选按钮就选中
            if (this.checkColumns.length == this.columns.length)
                this.checkAll = true
            else
                this.checkAll = false
        },



        handleClick() {
            // console.log(this.activeName);
            this.pageNum = 1
            this.load()

        },
        downloadTemplate() {
            // window.open("http://192.168.1.14:9090/file/210118f187944394ad5fc7f1cca2a028.xlsx")
            window.location.href = "http://192.168.1.14:9090/file/210118f187944394ad5fc7f1cca2a028.xlsx"

        },
        //获取所有数据：
        getAll() {
            this.request.get("/equipLedger").then(res => {
                const records = res.data;
                this.handleFilterField(records)

            });
        },
        handeleFilterChange(filter) {
            //在第二页筛选时，重新跳转到第一页
            this.pageNum = 1
            //联动筛选
            if (filter.validity)
                this.filtersForm.validity = filter.validity.join(',')
            if (filter.cycle)
                this.filtersForm.cycle = filter.cycle.join(',')
            if (filter.verOrg)
                this.filtersForm.verOrg = filter.verOrg.join(',')

            this.request.get("/equipLedger/filter", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    validity: this.filtersForm.validity,
                    verOrg: this.filtersForm.verOrg,
                    cycle: this.filtersForm.cycle
                }

            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total
                this.handleFilterField(res.data.records)
            });
        },
        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        //提交核查计划表单
        submit() {
            this.$refs.form.validate((valid) => {
                if (valid) {
                    this.request.post("/equipLedger", this.form).then(res => {
                        if (res.code === "200") {
                            this.$message.success("保存成功")
                            this.dialogVisible = false
                            this.activeName = this.form.type
                            this.load()
                        } else {
                            this.$message.error("保存失败")
                        }
                    })
                } else {
                    return false;
                }
            })
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },
        cancel() {
            this.handleClose()
        },
        //导出表格
        exp() {
            // window.location.href = "http://192.168.1.14:9090/equipLedger/export";
            window.location.href = "http://192.168.1.14:9090/equipLedger/export/" + this.activeName;
        },


        //导入成功加载钩子
        handleExcelImportSuccess() {
            this.$message.success("文件导入成功")
            this.load()
        },
        //导入失败加载钩子
        handleExcelImportFail() {
            this.$message.error("导入失败，请注意上传文件中的字段格式")
            this.load()
        },
        //查询重置
        //查询重置
        reset() {
            this.equipForm.name = ""
            this.equipForm.model = ""
            this.equipForm.validity = ""
            this.equipForm.cycle = ""
            this.equipForm.verOrg = ""
            this.activeName = "first"

            this.filtersForm.validity = ""
            this.filtersForm.verOrg = ""
            this.filtersForm.cycle = ""
            this.$refs.filterTable.clearFilter()
            this.load()
        },
        handleEdit(row) {
            this.modelType = 1
            // 注意需要对当前行数据进行深拷贝，否则会出错
            this.form = JSON.parse(JSON.stringify(row))
            this.dialogVisible = true

        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/equipLedger/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {

            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }

            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/equipLedger/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        load2() {
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/equipLedger/filter", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.equipForm.name,
                    type: this.activeName,
                    model: this.equipForm.model,
                    validity: this.equipForm.validity,
                    verOrg: this.equipForm.verOrg,
                    cycle: this.equipForm.cycle
                    // validity: this.filtersForm.validity,
                    // verOrg: this.filtersForm.verOrg,
                    // cycle:this.filtersForm.cycle
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total
                //重新加载数据时候需要清除下筛选变量
                this.getAll()

            }
            )
        },
        //给筛选字段赋值
        handleFilterField(records) {
            // 提取日期数据
            // 第一版：不能单独筛选
            const dates = records.map(record => record.validity);

            // 使用 Set 对象来获取唯一的日期数据
            const uniqueDates = [...new Set(dates)];
            // 排序日期数据
            uniqueDates.sort((a, b) => new Date(a) - new Date(b));
            // 更新 this.tableData 和 this.validityFilters
            // this.tableData = records;
            this.validityFilters = uniqueDates.map(date => {
                return {
                    text: date,
                    value: date
                };
            });



            //检定/校准机构
            const verOrgs = records.map(record => record.verOrg)
            const uniqueVerOrgs = [...new Set(verOrgs)];
            this.verOrgFilters = uniqueVerOrgs.map(verOrg => {
                return {
                    text: verOrg,
                    value: verOrg
                };
            });
            //周期
            const cycles = records.map(record => record.cycle)
            const uniqueCycles = [...new Set(cycles)];
            this.cycleFilters = uniqueCycles.map(cycle => {
                return {
                    text: cycle,
                    value: cycle
                };
            });
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleSizeChange(pageSize) {

            this.pageSize = pageSize
            this.load()
        },
        // changeEnable(row) {
        //     this.request.post("/equipLedger", row).then(res => {
        //         if (res.data.code === '200') {
        //             this.$message.success("操作成功")
        //         } else {
        //             this.$message.error("操作失败")
        //         }
        //     })
        // },
        tableRowClassName({ row, rowIndex }) {
            const today = new Date();
            const validityDate = new Date(row.validity);
            //校准日期+周期
            validityDate.setMonth(validityDate.getMonth() + row.cycle);

            // 计算校准日期与今天的天数差
            const daysRemaining = Math.floor((validityDate - today) / (1000 * 60 * 60 * 24) + 1);


            // 判断是否不到30天
            if (0 <= daysRemaining && daysRemaining <= 30) {

                // return this.selectedColor; // 返回用户选择的颜色作为类名
                return { 'highlight-row': true, 'top': '0' }; // 返回自定义类名
            }

            if (daysRemaining < 0) {
                return { 'gray-row': true, 'bottom': '0' }; // 返回自定义类名
            }

            return '';// 如果不应用额外类，可以考虑添加一个默认的排序权重
        },
        // open() {
        //     this.$confirm('是否将信息同步到固定资产?', '提示', {
        //         confirmButtonText: '确定',
        //         cancelButtonText: '取消',
        //         type: 'warning'
        //     }).then(() => {
        //         this.$refs.form.validate((valid) => {
        //             if (valid) {
        //                 Promise.all([
        //                     this.request.post("/equipLedger", this.form),
        //                     this.request.post("/equipment", this.form)
        //                 ]).then(responses => {
        //                     const equipLedgerRes = responses[0];
        //                     const equipmentRes = responses[1];
        //                     if (equipLedgerRes.code === "200" && equipmentRes.code === "200") {
        //                         // 两个请求都成功，关闭对话框
        //                         this.$message.success("保存成功");
        //                         this.dialogVisible = false;
        //                         // 其他逻辑，比如重新加载数据
        //                         this.load();
        //                     } else {
        //                         this.$message.error("保存失败");
        //                     }
        //                 });
        //             } else {
        //                 return false;
        //             }
        //         });
        //     }).catch(() => {
        //         this.$message({
        //             type: 'info',
        //             message: '已取消同步'
        //         });
        //     });
        // }
        open() {
            this.$confirm('是否将信息同步到固定资产?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$refs.form.validate((valid) => {
                    if (valid) {
                        this.request.post("/equipLedger/uuid", this.form)
                            .then(responses => {
                                const equipLedgerRes = responses;
                                if (equipLedgerRes.code === "200") {
                                    // 两个请求都成功，关闭对话框
                                    this.$message.success("保存成功");
                                    this.dialogVisible = false;
                                    // 其他逻辑，比如重新加载数据
                                    this.load();
                                } else {
                                    this.$message.error("保存失败");
                                }
                            });
                    } else {
                        return false;
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消同步'
                });
            });
        }
    },
};
</script>

