<template>
    <div class="manage">
        <!-- 页头区域 -->
        <el-form :inline="true" :model="roleForm" class="manage-header">
            <!-- 角色新增 -->
            <el-form-item>
                <el-button @click="handleAdd" type="primary">新增角色</el-button>
            </el-form-item>
            <!-- 批量删除 -->
            <el-form-item>
                <el-button @click="handleBatchDelete" style="margin-left: 10px;" type="danger">批量删除</el-button>
            </el-form-item>
            <!-- 搜索区 -->
            <el-form-item style="margin-left: 500px;">
                <el-input placeholder="请输入角色名" v-model="roleForm.name"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button @click="load2" type="primary">查询</el-button>
            </el-form-item>
        </el-form>
        <!-- 新增表单 -->
        <el-dialog title="提示" :visible.sync="dialogVisible" width="50%" :before-close="handleClose">
            <!-- 用户的表单信息 -->
            <el-form ref="form" :rules="rules" :inline="true" :model="form"  label-width="80px">
                <el-form-item label="角色名" prop="name">
                    <el-input placeholder="请输入角色名" v-model="form.name"></el-input>
                </el-form-item>
                <el-form-item label="有效期" prop="endTime">
                    <el-date-picker v-model="form.endTime" type="date" placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="备注" prop="remark">
                    <el-input placeholder="请输入备注" v-model="form.remark"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取 消</el-button>
                <el-button type="primary" @click="submit">确 定</el-button>
            </span>
        </el-dialog>

         <!-- 菜单分配 -->
         <el-dialog title="提示" :visible.sync="menuDialogVisible" width="30%" :before-close="handleCloseRoleMenu">
            <!-- 后台角色的菜单分配 -->
            <el-tree
                :props="props"
                :data="menuData"
                show-checkbox
                check-strictly="true"
                node-key="id"
                ref="tree"
                :default-expanded-keys="expends"
                :default-checked-keys="checks"
             >
            </el-tree>
            <span slot="footer" class="dialog-footer">
                <!-- TODO:取消按钮重置字段没写 -->
                <el-button @click="cancelRoleMenu">取 消</el-button>
                <el-button type="primary" @click="submitRoleBackMenu">确 定</el-button>
            </span>
        </el-dialog>

        <!-- 主要表格 -->
        <el-table height="90%" :data="tableData" style="width: 100%" border stripe
            @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="55" align="center"></el-table-column>
            <el-table-column type="index" label="序号" width="60" align="center" :index="indexMethod"></el-table-column>
            <el-table-column prop="name" label="角色名" align="center"></el-table-column>
            <el-table-column prop="endTime" label="有效期" align="center"></el-table-column>
            <el-table-column prop="remark" label="备注" align="center"></el-table-column>
            <el-table-column prop="enable" label="启用" align="center" width="80">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.enable" active-color="#13ce66" inactive-color="#ccc"
                        @change="changeEnable(scope.row)"></el-switch>
                </template>
            </el-table-column>
            <el-table-column label="操作" width="280" align="center">
                <template slot-scope="scope">
                   
                    <el-button style="margin-left:5px" size="mini" type="success" @click="selectMenu(scope.row.id)">分配菜单
                        <i class="el-icon-menu"></i>
                    </el-button>
                    <el-button style="margin-left:5px" size="mini" type="danger" @click="handleDelete(scope.row.id)">删除
                    </el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 页码 -->
        <div style="padding: 10px 0">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>

    </div>
</template>

<script>
export default {
    data() {
        return {
            tableData: [],
            modelType: 0,//0表示新增的弹窗，1表示编辑,
            multipleSelection: [],
            total: 0,//当前的总条数
            pageNum: 1,
            pageSize: 10,
            roleForm: {
                name: "",
            },
            form: {
                name: "",
                endTime: "",
                remark: "",
                enable: "",
            },
            dialogVisible: false,
            menuDialogVisible: false,
            rules: {
                name: [
                    { required: true, message: "请输入姓名", trigger: "blur" }
                ],
                endTime: [
                    { required: true, message: "请选择有效期", trigger: "blur" }
                ],
            },
            props: {
                label:'name',
            },
            expends:[],
            checks:[],
            roleId: 0,
            menuData:[],
        }
    },
    created() {
        this.load()
    },
    methods: {

        //分配菜单
        selectMenu(roleId){
            this.menuDialogVisible = true
              //请求菜单数据
            this.roleId = roleId
            this.request.get("/backMenu").then(res => {
                this.menuData = res.data
                //把后台返回的菜单数据处理成 id数组
                this.expends = this.menuData.map(v=> v.id)
            })

            //获取复选框信息
            this.request.get("/roleBackMenu/"+this.roleId).then(res => {
              
                this.checks = res.data
            })
        },
        submitRoleBackMenu(){
       
            this.request.post("/roleBackMenu/"+this.roleId,this.$refs.tree.getCheckedKeys()).then(res => {
                    // console.log(res)
                    if (res.code === "200") {
                        this.$message.success("绑定成功")
                        this.menuDialogVisible = false
                    }else{
                        this.$message.error("绑定失败："+res.msg)
                        
                    }
            })
        },
    //     handleCheckChange(data, checked, indeterminate) {
    //     // console.log(data, checked, indeterminate);
    //   },

        indexMethod(index) {
            let curpage = this.pageNum   //单前页码，具体看组件取值
            let limitpage = this.pageSize   //每页条数，具体是组件取值
            return (index + 1) + (curpage - 1) * limitpage
        },
        submit() {
            this.$refs.form.validate((valid)=>{
            if(valid){
            this.request.post("/role", this.form).then(res => {   //res为后台返回的结果
                if (res.code === "200") {
                    this.$message.success("保存成功")
                    this.dialogVisible = false
                    this.load()
                } else {
                    this.$message.error("保存失败")
                }
            })
            }else{
                     return false;                    
                    }
                    })
        
        },
        cancel() {
            this.handleClose()
        },
        handleClose() {
            this.$refs.form.resetFields()
            this.dialogVisible = false
        },

        cancelRoleMenu(){
            this.handleCloseRoleMenu()
        },
        handleCloseRoleMenu() {
            this.$refs.tree.setCheckedKeys([])
            this.menuDialogVisible = false
        },
        handleAdd() {
            this.modelType = 0
            this.dialogVisible = true  //弹窗
            this.form = {}
        },
        handleDelete(id) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.delete("/role/" + id).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleSelectionChange(val) {
            console.log(val)
            this.multipleSelection = val
        },
        handleBatchDelete() {
            if (this.multipleSelection.length === 0) {
                this.$message.warning("请先选择需要删除的内容");
                return;
            }
            let ids = this.multipleSelection.map(v => v.id)  //将对象数组[{},{}...]变成纯id的数组[1.2.3]
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.request.post("/role/del/batch", ids).then(res => {
                    if (res.code === '200') {
                        this.$message({
                            type: 'success',
                            message: '批量删除成功!'
                        });
                    }
                    // 重新获取列表的接口
                    this.load()
                })

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消批量删除'

                });
                this.load()
            });
        },
        load2(){
            this.pageNum = 1
            this.load()
        },
        load() {
            this.request.get("/role/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    name: this.roleForm.name
                }
            }).then(res => {
                this.tableData = res.data.records
                this.total = res.data.total

            })
            
          
            // fetch("http://localhost:9090/role/page?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize + "&name=" + this.roleForm.name)
            //     .then(res => res.json()).then(
            //         res => {
            //             // console.log(res)
            //             this.tableData = res.data.records
            //             this.total = res.data.total
            //         }
            //     )
        },
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handleFileUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            //   console.log(pageSize)
            this.pageSize = pageSize
            this.load()
        },
        changeEnable(row) {
            // console.log("1111")
            this.request.put("/role", row).then(res => {
                if (res.code === '200') {
                    this.$message.success("操作成功")
                } else {
                    this.$message.error("操作失败")
                }
            })
        },
    },
};
</script>

<style lang="less" scoped>
.manage {
    height: 90%;

    .manage-header {
        display: flex;
        // justify-content: space-between;
        align-items: center;
    }

    .common-table {
        position: relative;
        height: calc(100% - 62px);

        .pager {
            // position: absolute;
            // bottom: 0;
            // right: 20px;
            padding: 10px 0;
        }
    }
}
</style>