<template>
  <div class="login_container" style="background-size: cover; background-position: center;">
 
    <div class="login_box" style="background: rgba(255, 255, 255, 0.5);">
      <!-- 页头区域 -->
      <div class="abc">系 统 登 录 界 面</div>
      <div class="avatar_box">
        <img src="../assets/sanfangdianqi.png" alt="三方电气" class="logo_image" style="width: 160%; height: 40%;">
      </div>
      
      <!-- 登录表单区域 -->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" label-width="0px" class="login_form">
        <!-- 用户名 -->
        <!-- <el-form-item prop="loginName">
          <el-input v-model="loginForm.loginName" prefix-icon="iconfont icon-user" placeholder="请输入用户名" ></el-input>
        </el-form-item> -->
     
        <el-form-item prop="loginName">
          <el-autocomplete :hide-loading="true" :highlight-first-item="true" v-model="loginForm.loginName" :fetch-suggestions="fetchLoginName" 
          prefix-icon="iconfont icon-user" placeholder="请输入或选择用户名"
           style="width: 405px;"></el-autocomplete>
        </el-form-item>

        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="iconfont icon-3702mima" type="password" placeholder="请输入密码"></el-input>
        </el-form-item>
        <!-- 按钮区域 -->
      <el-form-item class="btns" style="display: flex; justify-content: center;">
        <el-button type="primary" @click="login" @keyup.enter="login()">登录</el-button>
        <el-button type="info" @click="resetLoginForm">重置</el-button>
      </el-form-item>
      </el-form>
    </div>
  </div>
  
</template>
