                </el-breadcrumb>
            </span>
            <el-divider></el-divider>
            <span>
                <div v-for="item in fileList" :key="item.id" class="projectDetail">
                    <i class="el-icon-document" :style="{ fontSize: '80px', color: 'black' }"
                        @click="download(item.path)"></i>
                    <div style="text-align: center;overflow:hidden;color: black;font-size: 13px;" @click="download">
