        <!-- 页码 -->
        <div class="page">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[2, 5, 10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
  
<script>
import HomeHeader from "../../../components/HomeHeader.vue";
import HomeFooter from "../../../components/HomeFooter.vue";
export default {
    components: { HomeHeader, HomeFooter },
    data() {
        return {
            tableData: [],
            pageNum: 1,
            pageSize: 10,
            fileList: [],
            total: 0,
            id: localStorage.getItem("projectId"),
            projectName: localStorage.getItem("projectName") ? JSON.parse(localStorage.getItem("projectName")) : {},
            path: "",
        }
    },
    created() {
        this.load()
    },
    methods: {
        handleCurrentChange(pageNum) {
            this.pageNum = pageNum
            this.load()
        },
        handlenewsUploadSuccess() {
            this.$message.success("上传成功")
            this.load()
        },
        handleSizeChange(pageSize) {
            this.pageSize = pageSize
            this.load()
        },
        load() {
            this.request.get("/projectFile/page", {
                params: {
                    pageNum: this.pageNum,
                    pageSize: this.pageSize,
                    projectId: this.id,
                }
            }).then(res => {
                this.fileList = res.data.records
                this.total = res.data.total

            })
            // fetch("http://localhost:9090/projectFile/page?pageNum=" + this.pageNum + "&pageSize=" + this.pageSize + "&projectId=" + this.id)
            //     .then(res => res.json()).then(
            //         res => {
            //             console.log(res)
            //             this.fileList = res.data.records
            //             this.total = res.data.total
            //         }
            //     )
        },
        projectDetail() {
            this.$router.push({ path: '/projectdetail' })
        },
        myproject() {
            this.$router.push({ path: '/myproject' })
        },
        download(path) {
            window.open(path)
        },
    },
}
</script>
  
